      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit"
      >
      <div slot="body">
        <el-form status-icon label-width="120px" >
              <el-form-item label="BARCODE" prop="checkPass">
                <el-input type="number" autofocus="true" v-model="redeem_barcode" placeholder="Customer Barcode" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="PASSWORD" prop="checkPass">
                <el-input type="password" v-model="redeem_password" placeholder="User Password" auto-complete="off"></el-input>
              </el-form-item>
            </el-form>
      </div>
    </app-modal>
      <div>
        <template>
          <br>
          <el-row>
            <el-col :span="4">
              <span class="demo-input-label" style="width: auto;">Search Customer:&nbsp;</span>
            </el-col>
            <el-col :span="8">
              <el-select :remote-method="remoteMethod" v-model="selectedCustomer" value-key="id" filterable remote reserve-keyword placeholder="Search Here ...." :loading="loading" style="margin-left: -39px;margin-top: -8px;width: 105%" @change="selectCustomerEvent">
                <el-option
                label="View All"
                :value="0">
                </el-option>
                <el-option
                v-for="item in states"
                :key="item.id"
                :label="item.first_name +' '+ item.last_name"
                :value="item">
                </el-option>
                </el-select>
              </el-col>
              <el-col :span="4">
                <span class="demo-input-label" style="width: auto;">Select Shift: &nbsp;</span>
              </el-col>
              <el-col :span="8">
                <el-form>
                  <el-select @change="selectShiftOnly" clearable v-model="selectShift" style="width: 103%;margin-left: -85px;margin-top: -8px" placeholder="----- SELECT -----">
                    <el-option label="Shift 1" value="1"></el-option>
                    <el-option label="Shift 2" value="2"></el-option>
                    <el-option label="Shift 3" value="3"></el-option>
                  </el-select>
                </el-form>
              </el-col>
            </el-row>
            <br>
              <el-radio-group v-model="radio">
                <el-row style="margin-left: 87px">
                  <el-form>
                    <el-col :span="1">
                      <el-form-item>
                        <el-radio :label="true">&nbsp;</el-radio>
                      </el-form-item>
                    </el-col>
                    <el-col :span="7">
                    <el-date-picker
                      @change="selectDateOnly"
                      style="margin-left: 27px;width: 355px"
                      :disabled="!radio"
                      v-model="dateOnly"
                      type="date"
                      placeholder="Pick a date"
                      value-format="yyyy-MM-dd">
                    </el-date-picker>
                  </el-col>
                  </el-form>
                  <span class="demo-input-label" style="width: auto;">Date</span>
                  <el-form style="
                    padding: 0px;
                    margin-top: -20px;
                    margin-left: 105%">
                    <el-col :span="1">
                    <el-form-item>
                      <el-radio :label="false">&nbsp;</el-radio>
                    </el-form-item>
                    </el-col>
                    <el-col :span="7">
                    <el-date-picker
                      style="margin-left: 55px"
                      v-model="dateFromTo"
                      :disabled="radio"
                      @change="selectDateEvent"
                      type="daterange"
                      align="right"
                      start-placeholder="Start Date"
                      end-placeholder="End Date"
                      value-format="yyyy-MM-dd">
                    </el-date-picker>
                  </el-col>
                </el-form>
              </el-row>
            </el-radio-group>
          <br /> <br />
          <el-card>
            <el-row>
              <el-col :span="7"><p class="customer_name">CUSTOMER: <b>{{selectedCustomer ? fullName : '' }}</b></p></el-col>
              <el-col :span="7"><p class="barcode">BARCODE: <b>{{selectedCustomer.barcode}}</b></p></el-col>
            </el-row>
          </el-card>
        </template>
      </div>
      <br>
      <el-tabs type="border-card">
        <el-tab-pane label="TRANSACTIONS">
          <el-table
            :data="transactions"
            border
            style="width: 100%"
            height="400">
            <el-table-column
            prop="created_at"
            label="DATE"
            fixed
            width="133">
            <template slot-scope="scope">
              {{ toDateString(scope.row.created_at) }}
            </template>
            </el-table-column>
            <el-table-column
            prop="created_at"
            label="ENCODED TIME"
            fixed
            width="133">
            <template slot-scope="scope">
              {{ toDateTimeString(scope.row.created_at) }}
            </template>
            </el-table-column>
            <el-table-column
            prop="product_id"
            label="PRODUCT"
            width="133">
            <template slot-scope="scope">
              {{ scope.row.product.product_name }}
            </template>
            </el-table-column>
            <el-table-column
            prop="price"
            label="PRICE"
            width="133">
            </el-table-column>
            <el-table-column
            prop="liters"
            label="LITERS/QTY"
            width="133">
            </el-table-column>
            <el-table-column
            prop="liters"
            label="DEBIT"
            width="133">
            </el-table-column>
            <el-table-column
            prop="amount"
            label="TOTAL AMOUNT"
            width="133">
            </el-table-column>
            <el-table-column
            label="CREDIT"
            width="133">
            <template slot-scope="scope">
              {{ scope.row.redeem[0] ? scope.row.redeem[0].amount : '' }}
            </template>
            </el-table-column>
            <el-table-column
            prop="running_points"
            label="RUNNING POINTS"
            width="143">
            <template slot-scope="scope">
              {{ (scope.row.running_points).toFixed(2) }}
            </template>
            </el-table-column>
            <el-table-column
            prop="remarks"
            label="REMARKS"
            width="133">
            </el-table-column>
          </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
          </app-pagination>
          <br>
      <div>
        <template>
            <div style="float: right">
              <el-form :inline="true" class="demo-form-inline">
                <el-form-item label="CURRENT POINTS">
                    <el-input v-model="totalpoints" readonly></el-input>
                </el-form-item>
                <el-form-item label="TOTAL AMOUNT">
                    <el-input v-model="totalamount" placeholder="2275" readonly></el-input>
                </el-form-item>
              </el-form>
            </div>
        </template>
      </div>
        </el-tab-pane>
        <el-tab-pane label="REDEEMED">
          <div>
            <el-col :span="22" :offset="1">
              <el-table
            :data="redeemPoints"
            border
            height="400">
            <el-table-column
            prop="serial_no"
            label="SERIAL NUMBER">
            </el-table-column>
            <el-table-column
            prop="created_at"
            label="DATE">
            <template slot-scope="scope">
              {{ toDateString(scope.row.created_at) }}
            </template>
            </el-table-column>
            <el-table-column
            prop="created_at"
            label="ENCODED TIME">
            <template slot-scope="scope">
              {{ toDateTimeString(scope.row.created_at) }}
            </template>
            </el-table-column>
            <el-table-column
            prop="customer_id"
            label="CUSTOMER NAME">
            <template slot-scope="scope">
              {{ scope.row.customer.first_name }} {{ scope.row.customer.last_name }}
            </template>
            </el-table-column>
            <el-table-column
            prop="amount"
            label="AMOUNT">
            </el-table-column>
          </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
          </app-pagination>
            </el-col>
          </div>
        </el-tab-pane>
      </el-tabs>
      <br>
      <div>
        <template>
            <div style="float: right">
              <br>
              <p>TOTAL POINTS <b class="margin">{{ totalpoints }}</b></p>
              <p>POINTS REDEEMED <b class="margin2">{{ totalpointsredeem }}</b></p>
              <br>
            <div style="float: right">
              <el-button class="submit" @click="exportExcel" icon="fa fa-download">&nbsp;EXPORT TO EXCEL</el-button>
              <br><br>
            </div>
            </div>
        </template>
      </div>
</div>
</template>
<script>
import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import RedeemModal from '@/components/RedeemModal'
import _ from 'lodash'
export default {
  name: 'CustomerPoints',
  data () {
    return {
      fromDate: '',
      toDate: '',
      oneDate: '',
      shiftOnly: '',
      customerOnly: '',
      selectShift: '',
      dateOnly: '',
      dateFromTo: '',
      radio: true,
      page: 1,
      dateFrom: '',
      from_date: '',
      to_date: '',
      loading: false,
      list: [],
      redeem_barcode: '',
      redeem_password: '',
      redeemDialog: false,
      amount_redeem: '',
      current_points: '',
      total_amounts: '',
      selectedCustomer: [],
      transactionInfo: [],
      exportValue: 1
    }
  },
  computed: {
    fullName: function () {
      return this.selectedCustomer.first_name ? this.selectedCustomer.first_name + ' ' + this.selectedCustomer.last_name : ''
    },
    totalamount: function () {
      if (this.transactions.length > 0) {
        const amountArr = this.transactions.map(el => parseFloat(el.amount))
        const reducer = (accumulator, currentValue) => accumulator + currentValue
        const finalAmount = amountArr.reduce(reducer).toFixed(2)
        return finalAmount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      }
      return 0
    },
    totalpoints: function () {
      if (this.transactions.length > 0) {
        const amountArr = this.transactions.map(el => parseFloat(el.points))
        const reducer = (accumulator, currentValue) => accumulator + currentValue
        const finalPoints = amountArr.reduce(reducer).toFixed(2)
        return finalPoints.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      }
      return 0
    },
    totalpointsredeem: function () {
      if (this.redeemPoints.length > 0) {
        const amountArr = this.redeemPoints.map(el => el.amount)
        const reducer = (accumulator, currentValue) => accumulator + currentValue
        return amountArr.reduce(reducer).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      }
      return 0
    },
    displayResult () {
      if (this.customerTransaction.length > 0) {
        return this.customerTransaction[0]
      }
      return ' '
    },
    ...mapGetters('transaction', {
      transactions: 'transactions',
      pagination: 'pagination'
    }),
    ...mapGetters('customer', {
      states: 'customers'
    }),
    ...mapGetters('pointsRedeem', {
      redeemPoints: 'redeemPoints',
      pagination: 'pagination'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('transaction', {
      customerTransaction: 'customerTransaction'
    })
  },
  watch: {
    page () {
      this.getTransactions()
      this.getPointsRedeem()
    }
  },
  created () {
    this.getTransactions()
    this.getCustomers()
    this.getPointsRedeem()
  },
  methods: {
    remoteMethod: _.debounce(function (query) {
      this.loading = true
      this.$store.dispatch('customer/getCustomers', {
        s: query,
        s_field: 'first_name,last_name'
      }).then(() => {
        this.loading = false
      })
    }, 1000),
    exportExcel () {
      this.fromDate = this.dateFromTo[0] ? this.dateFromTo[0] : 0
      this.toDate = this.dateFromTo[1] ? this.dateFromTo[1] : 0
      this.oneDate = this.dateOnly ? this.dateOnly : 0
      this.shiftOnly = this.selectShift ? this.selectShift : 0
      this.customerOnly = this.selectedCustomer.id ? this.selectedCustomer.id : 0
      if (this.customerOnly === 0 && this.fromDate === 0 && this.oneDate === 0 && this.shiftOnly === 0) {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/transaction?filter_by_customer=' + this.customerOnly + '&filter_by_shift=' + this.shiftOnly + '&export=' + this.exportValue
      } else if (this.fromDate !== 0 && this.toDate !== 0) {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/transaction?filter_by_customer=' + this.customerOnly + '&from_date=' + this.fromDate + '&to_date=' + this.toDate + '&filter_by_shift=' + this.shiftOnly + '&export=' + this.exportValue
      } else {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/transaction?filter_by_customer=' + this.customerOnly + '&filter_customer_date=' + this.oneDate + '&filter_by_shift=' + this.shiftOnly + '&export=' + this.exportValue
      }
      window.location.href = this.url
    },
    submitForm () {
      if (this.totalpoints >= 1000) {
        this.redeemDialog = true
      } else if (this.fullName === '') {
        this.$notify({
          title: 'Error',
          message: 'Please Select Customer',
          type: 'error',
          duration: '3000'
        })
      } else {
        this.$notify({
          title: 'Error',
          message: 'Points Must be Greater than 1000 before you can Redeem',
          type: 'error',
          duration: '3000'
        })
      }
    },
    async onSubmit () {
      if (this.redeem_barcode !== '' || this.redeem_password !== '') {
        this.$notify({
          title: 'Success',
          message: 'SuccessFully Redeem!',
          type: 'success',
          duration: 2000
        })
        this.redeemDialog = false
        this.clear_form()
      } else {
        this.$notify.error({
          title: 'Error',
          message: 'Something went wrong!',
          duration: 2000
        })
      }
    },
    clear_form: function () {
      this.from_date = ''
      this.to_date = ''
    },
    getCustomers () {
      this.$store.dispatch('customer/getCustomers', { page: null })
    },
    getPointsRedeem () {
      const page = this.page
      this.$store.dispatch('pointsRedeem/getPointsRedeem', {
        customer_id: 0,
        from_date: 0,
        page,
        to_date: 0,
        shift: 0,
        dateOnly: 0,
        export: 0
      })
    },
    getTransactions () {
      const page = this.page
      this.$store.dispatch('transaction/getTransactions', {
        customer_id: 0,
        page,
        from_date: 0,
        to_date: 0,
        dateOnly: 0,
        shift: 0,
        export: 0
      })
    },
    selectCustomerEvent () {
      if (this.selectedCustomer.id !== undefined) {
        const page = this.page
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: this.selectedCustomer.id,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.selectShift ? this.selectShift : 0,
          export: 0
        })
        this.selectCustomerRedeem()
      } else {
        const page = this.page
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: 0,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: 0,
          export: 0
        })
        this.selectCustomerRedeem()
      }
    },
    selectCustomerRedeem () {
      if (this.selectedCustomer.id !== undefined) {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: this.selectedCustomer.id,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.selectShift ? this.selectShift : 0,
          export: 0
        })
      } else {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: 0,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: 0,
          export: 0
        })
      }
    },
    selectShiftOnly () {
      if (this.selectShift !== undefined) {
        const page = this.page
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.selectShift,
          export: 0
        })
        this.selectShiftRedeem()
      } else {
        const page = this.page
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: 0,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: 0,
          export: 0
        })
        this.selectShiftRedeem()
      }
    },
    selectShiftRedeem () {
      if (this.selectShift !== undefined) {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.selectShift,
          export: 0
        })
      } else {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: 0,
          page,
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: 0,
          export: 0
        })
      }
    },
    selectDateEvent () {
      const page = this.page
      if (this.dateFromTo !== null) {
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          from_date: this.dateFromTo[0],
          to_date: this.dateFromTo[1],
          page,
          dateOnly: 0,
          shift: this.selectShift ? this.selectShift : 0,
          export: 0
        })
        this.selectDateRedeem()
      } else {
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          from_date: 0,
          to_date: 0,
          page,
          dateOnly: 0,
          shift: this.shiftOnly ? this.shiftOnly : 0,
          export: 0
        })
        this.selectDateRedeem()
      }
    },
    selectDateRedeem () {
      if (this.dateFromTo !== null) {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          page,
          from_date: this.dateFromTo[0],
          to_date: this.dateFromTo[1],
          dateOnly: 0,
          shift: this.selectShift ? this.selectShift : 0,
          export: 0
        })
      } else {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          page,
          from_date: 0,
          to_date: 0,
          dateOnly: 0,
          export: 0,
          shift: this.shiftOnly ? this.shiftOnly : 0
        })
      }
    },
    selectDateOnly () {
      const page = this.page
      if (this.dateOnly !== null) {
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          from_date: 0,
          page,
          to_date: 0,
          dateOnly: this.dateOnly,
          shift: this.selectShift ? this.selectShift : 0,
          export: 0
        })
        this.selectDateOnlyRedeem()
      } else {
        const page = this.page
        this.$store.dispatch('transaction/getTransactions', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          from_date: 0,
          to_date: 0,
          page,
          dateOnly: 0,
          shift: this.shiftOnly ? this.shiftOnly : 0,
          export: 0
        })
        this.selectDateOnlyRedeem()
      }
    },
    selectDateOnlyRedeem () {
      if (this.dateOnly !== null) {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          page,
          from_date: 0,
          to_date: 0,
          dateOnly: this.dateOnly,
          export: 0,
          shift: this.selectShift ? this.selectShift : 0
        })
      } else {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: this.selectedCustomer.id ? this.selectedCustomer.id : 0,
          page,
          from_date: 0,
          to_date: 0,
          dateOnly: 0,
          export: 0,
          shift: this.shiftOnly ? this.shiftOnly : 0
        })
      }
    },
    onCancel () {
      this.redeemDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-modal': RedeemModal,
    'app-pagination': Pagination
  }
}
</script>
