import api from '../api/auth'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    user: {},
    isLogin: false,
    token_type: '',
    access_token: '',
    shift: ''
  },
  getters: {
    isLogin: state => state.isLogin,
    token_type: state => state.token_type,
    access_token: state => state.access_token,
    shift: state => state.shift,
    currentUser: state => state.user
  },
  mutations: {
    [types.mutations.auth.setIsLogin] (state, payload) {
      state.isLogin = payload
    },
    [types.mutations.auth.setToken] (state, payload) {
      state.token_type = payload.token_type
      state.access_token = payload.access_token
    },
    [types.mutations.auth.setShift] (state, payload) {
      state.shift = payload
    },
    [types.mutations.auth.setUser] (state, payload) {
      state.user = payload.data
    }
  },
  actions: {
    async [types.actions.auth.authLogin] ({ commit }, payload) {
      try {
        const response = await api.login(payload)
        if (response.status === 200) {
          commit(types.mutations.auth.setToken, response.data)
          const user = await api.getAuthUser(response.data.access_token)
          commit(types.mutations.auth.setUser, user.data)
        }
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.auth.getDatabase] (payload) {
      try {
        await api.exportDatabase(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
