export default {
  getPrices (payload = {}) {
    const page = payload.page !== null ? `?page=${payload.page}` : ''
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/price' + page)
  },
  addPrice (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/price', payload)
  },
  updatePrice (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/price/${payload.id}`, payload)
  },
  deletePrice (payload = {}) {
    return axios.delete(process.env.VUE_APP_API_ENDPOINT + `/price/${payload.id}`, payload)
  }
}
