import api from '../api/customer'
import types from '../../types'

export default {
  namespaced: true,
  state: {
    customers: [],
    pagination: '',
    customer: []
  },
  getters: {
    customers: state => state.customers,
    pagination: state => state.pagination,
    customer: state => state.customer
  },
  mutations: {
    [types.mutations.customers.setCustomers] (state, payload) {
      if (payload.data === undefined) {
        state.customers = payload
        state.pagination = payload
      } else {
        state.customers = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.customers.setSelectedCustomer] (state, payload) {
      state.customer = payload
    }
  },
  actions: {
    async [types.actions.customers.getCustomers] ({ commit }, payload) {
      try {
        const response = await api.getCustomers(payload)
        commit(types.mutations.customers.setCustomers, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customers.addCustomer] (state, payload) {
      try {
        await api.addCustomer(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customers.updateCustomer] (state, payload) {
      try {
        await api.updateCustomer(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customers.deleteCustomer] (state, payload) {
      try {
        await api.deleteCustomer(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
