<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <el-row :gutter="10">
      <el-col :span="10">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-plus"></i> Add User</span>
          </div>
          <el-form :model="form" label-width="90px">
          <el-form-item label="Username">
            <el-input name="Username" :class="errors.has('Username')? 'is-error':''" v-validate="'required'" v-model="ruleForm.username" placeholder="Enter Username"></el-input>
            <span v-show="errors.has('Username')" class="text-error">{{ errors.first('Username') }}</span>
          </el-form-item>
          <el-form-item label="Email">
            <el-input name="Email" type="email" :class="errors.has('Email')? 'is-error':''" v-validate="'required'" v-model="ruleForm.email" placeholder="Enter Email"></el-input>
            <span v-show="errors.has('Email')" class="text-error">{{ errors.first('Email') }}</span>
          </el-form-item>
            <el-form-item label="Role Type" prop="roleType">
            <!-- v-validate="'required'" -->
            <el-select name="Role Type" style="width: 100%" :class="errors.has('Role Type')? 'is-error':''" v-model="ruleForm.role_name" placeholder="----- SELECT -----">
              <el-option
                  v-for="item in roleName"
                  :key="item.id"
                  :label="item.display_name"
                  :value="item.name"
                  >
                </el-option>
            </el-select>
            <span v-show="errors.has('Role Type')" class="text-error">{{ errors.first('Role Type') }}</span>
          </el-form-item>
          <el-form-item label="Password" v-show="isShow">
            <el-input name="Password" type="password" minlength="6" v-model="ruleForm.password" placeholder="Password"></el-input>
          </el-form-item>
          <el-form-item label="Confirm" v-show="isShow">
            <el-input name="Confirm Password" type="password" minlength="6" v-model="ruleForm.confirm_password" placeholder="Confirm Password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button style="float: right" v-if="isEdit" :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> UPDATE</el-button>
            <el-button style="float: right" v-else :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
          </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="14">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-list"></i> List of User</span>
          </div>
          <el-table
            :data="users"
            border
            style="width: 100%">
            <el-table-column
              prop="username"
              label="Name"
              width="120">
            </el-table-column>
            <el-table-column
              prop="email"
              label="Email"
              width="160">
            </el-table-column>
            <el-table-column
            label="Role Type">
            <template slot-scope="scope">
              {{ scope.row.roles.data[0].display_name }}
            </template>
            </el-table-column>
            <el-table-column
              label="Action"
              width="110">
              <template slot-scope="scope">
                <el-button
                  icon="fa fa-pencil"
                  title="Edit Customer Information"
                  type="success"
                  circle
                  v-show="disableDelete(scope.row)"
                  @click="handleEdit(scope.row)">
                </el-button>
                <el-button
                  icon="fa fa-trash"
                  title="Delete Customer Information"
                  class="colorPrimary"
                  circle
                  v-show="disableDelete(scope.row)"
                  @click="handleDelete(scope.row)"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- <app-pagination :pageSize="5" :total="tableData.length">
          </app-pagination> -->
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import Modal from '@/components/Modal'

export default {
  data () {
    return {
      isTrue: true,
      isFalse: false,
      title: '',
      deleteDialog: false,
      isShow: true,
      isEdit: false,
      ruleForm: {
        id: '',
        role_name: '',
        username: '',
        email: '',
        password: '',
        confirm_password: ''
      },
      form: {
        name: '',
        resource: ''
      },
      roleName: [],
      usersInfo: [],
      tableData: []
    }
  },
  created () {
    this.getRoles()
    this.getUsers()
    this.hideAdmin()
  },
  computed: {
    ...mapGetters({
      users: 'user/users',
      roles: 'user/roles',
      response: 'user/response'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('auth', {
      user: 'currentUser'
    })
  },
  methods: {
    hideAdmin () {
      const roleName = this.roles.filter(el => el.name !== 'admin')
      const userRole = this.user.roles.data[0].name
      if (userRole === 'sic') {
        this.roleName = roleName
      } else {
        this.roleName = this.roles
      }
    },
    getRoles () {
      this.$store.dispatch('user/getRoles')
    },
    getUsers () {
      this.$store.dispatch('user/getUsers')
    },
    clear_form: function () {
      this.ruleForm = {
        username: '',
        email: '',
        password: '',
        confirm_password: '',
        role_name: ''
      }
    },
    onCancel () {
      this.deleteDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    handleEdit (item) {
      this.isShow = false
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
      this.ruleForm.role_name = item.roles.data[0].display_name
      this.ruleForm.id = item.id
    },
    handleDelete (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.usersInfo = Object.assign({}, item)
    },
    submitForm () {
      const form = {
        id: this.ruleForm.id,
        username: this.ruleForm.username,
        email: this.ruleForm.email,
        password: this.ruleForm.password,
        role_name: this.ruleForm.role_name
      }
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updateUser(form)
          } else {
            this.addUser(form)
          }
        }
      })
    },
    async addUser (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('user/addUser', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'User Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.users.push(this.response)
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async updateUser (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('user/updateUser', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'User Successfully Updated!',
            type: 'success',
            duration: 2000
          })
          this.getUsers()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('user/deleteUser', this.usersInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'SuccessFully Deleted!',
            type: 'success',
            duration: 2000
          })
          const index = this.users.findIndex(value => value.id === this.usersInfo.id)
          this.users.splice(index, 1)
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    disableDelete (data) {
      const selectedAdmin = data.roles.data[0].name
      if (selectedAdmin === 'admin') {
        return false
      } else {
        return true
      }
    },
    validateForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.$notify({
            title: 'Success',
            message: 'Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.$nextTick(() => {
            this.$validator.reset()
          })
          this.clear_form()
        }
      })
    }
  },
  components: {
    'app-pagination': Pagination,
    'app-modal': Modal
  }
}
</script>

<style scoped>
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple {
  background: #99a9bf;
}
.clearfix {
  font-size: 18px;
}
</style>
