import Vue from 'vue'
import Vuex from 'vuex'
// all modules will be imported in this section
import auth from './modules/auth'
import appUI from './modules/appUI'
import dbConnection from './modules/dbConnection'
import transaction from './modules/transaction'
import customer from './modules/customer'
import user from './modules/user'
import vehicleManagement from './modules/vehicleManagement'
import productManagement from './modules/productManagement'
import priceManagement from './modules/priceManagement'
import customerGroup from './modules/customerGroup'
import pointsSystem from './modules/pointsSystem'
import createPersistedState from 'vuex-persistedstate'
import pointsRedeem from './modules/pointsRedeem'
import databaseManagement from './modules/databaseManagement'

Vue.use(Vuex)

export const store = new Vuex.Store({
  modules: {
    auth,
    appUI,
    dbConnection,
    transaction,
    customer,
    productManagement,
    priceManagement,
    user,
    customerGroup,
    pointsSystem,
    vehicleManagement,
    pointsRedeem,
    databaseManagement
  },
  plugins: [
    createPersistedState()
  ]
})
