<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <el-button @click="exportProductPrice" class="submit" style="width: 23%;float: right" icon="fa fa-download">
    EXPORT TO EXCEL &nbsp;
    </el-button>
    <br><br><br>
    <el-row :gutter="10">
      <el-col :span="10">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-edit"></i> New Product Price</span>
          </div>
          <el-form :model="ruleForm" label-width="140px">
            <el-form-item label="SELECT DATE">
              <el-date-picker
                name="Date"
                v-model="ruleForm.product_date"
                type="date"
                placeholder="Pick a day"
                v-validate="'required'">
              </el-date-picker>
              <span class="text-error">{{ errors.first('Date') }}</span>
            </el-form-item>
            <el-form-item label="SELECT PRODUCT">
              <el-select v-model="ruleForm.product_id" placeholder="SELECT PRODUCT" v-validate="'required'" name="Product">
                <el-option
                  v-for="item in products"
                  :key="item.id"
                  :label="item.product_name"
                  :value="item.id"
                  >
                </el-option>
              </el-select>
              <span class="text-error">{{ errors.first('Product') }}</span>
            </el-form-item>
            <el-form-item label="ENTER PRICE">
              <el-input type="number" placeholder="Please input" v-model="ruleForm.product_price" name="Price" v-validate="'required'"></el-input>
              <span class="text-error">{{ errors.first('Price') }}</span>
            </el-form-item>
            <el-form-item>
              <el-button style="float: right" v-if="isEdit" :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> UPDATE</el-button>
              <el-button style="float: right" v-else :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="14">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-list"></i> List of Product Price</span>
          </div>
          <el-table
          :data="prices"
            border
            style="width: 100%">
            <el-table-column
              prop="product_id"
              label="PRODUCT">
              <template slot-scope="scope">
              {{ scope.row.product.product_name }}
              </template>
            </el-table-column>
            <el-table-column
              prop="product_price"
              label="PRICE">
            </el-table-column>
            <el-table-column
              label="Action">
              <template slot-scope="scope">
                <el-button
                  icon="fa fa-pencil"
                  title="Edit Customer Information"
                  type="success"
                  circle
                  @click="handleEdit(scope.row)">
                </el-button>
                <el-button
                  v-show="priceDisplay"
                  icon="fa fa-trash"
                  title="Delete Customer Information"
                  class="colorPrimary"
                  circle
                  @click="handleDelete(scope.row)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
         </app-pagination>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>

import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import Modal from '@/components/Modal'
import moment from 'moment'

export default {
  data () {
    return {
      priceDisplay: false,
      isEdit: false,
      name: '',
      ruleForm: {
        id: '',
        product_date: Date.now(),
        product_id: '',
        product_name: '',
        product_price: ''
      },
      title: '',
      deleteDialog: false,
      priceInfo: [],
      page: 1
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('priceManagement', {
      prices: 'prices',
      pagination: 'pagination',
      response: 'response'
    }),
    ...mapGetters('productManagement', {
      products: 'products'
    }),
    ...mapGetters('auth', {
      shift: 'shift'
    }),
    ...mapGetters('auth', {
      user: 'currentUser'
    })
  },
  watch: {
    page () {
      this.getPrices()
      this.getProducts()
    }
  },
  created () {
    this.getProducts()
    this.getPrices()
  },
  methods: {
    exportProductPrice () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/export-price-summary'
      window.location.href = this.url
    },
    getProducts () {
      this.$store.dispatch('productManagement/getProducts', {page: null})
    },
    getPrices () {
      const page = this.page
      this.$store.dispatch('priceManagement/getPrices', { page })
    },
    clear_form: function () {
      this.ruleForm = {
        product_id: '',
        product_price: '',
        product_date: Date.now()
      }
      this.$nextTick(() => {
        this.$validator.reset()
      })
    },
    submitForm () {
      const form = {
        id: this.ruleForm.id,
        product_date: moment(this.ruleForm.product_date).format('YYYY-MM-DD'),
        product_id: this.ruleForm.product_id,
        product_price: this.ruleForm.product_price,
        shift: this.shift,
        username: this.user.username
      }
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updatePrice(form)
          } else {
            this.addPrice(form)
          }
        }
      })
    },
    handleEdit (item) {
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
    },
    handleDelete (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.priceInfo = Object.assign({}, item)
    },
    async addPrice (form) {
      const name = this.prices.findIndex(el => el.product_id === this.ruleForm.product_id)
      if (name !== 0) {
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.commit('appUI/setIsDisable', true)
        this.$store.dispatch('priceManagement/addPrice', form)
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            this.$store.commit('appUI/setIsDisable', false)
            this.$notify({
              title: 'Success',
              message: 'Successfully Saved!',
              type: 'success',
              duration: 2000
            })
            this.prices.unshift(this.response)
          })
          .catch(error => {
            this.$store.commit('appUI/setIsLoading', false)
            this.$store.commit('appUI/setIsDisable', false)
            this.$notify({
              title: 'Error',
              message: 'Something went wrong!',
              type: 'error'
            })
            throw new Error(error)
          })
      } else {
        this.$notify({
          title: 'Error',
          message: 'Product with Price is already exist!',
          type: 'error'
        })
      }
      this.clear_form()
    },
    async updatePrice (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('priceManagement/updatePrice', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Product Updated!',
            type: 'success',
            duration: 2000
          })
          this.getPrices()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('priceManagement/deletePrice', this.priceInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'SuccessFully Deleted!',
            type: 'success',
            duration: 2000
          })
          const index = this.prices.findIndex(value => value.id === this.priceInfo.id)
          this.prices.splice(index, 1)
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.deleteDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination,
    'app-modal': Modal
  }
}
</script>

<style scoped>
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.clearfix {
  font-size: 18px;
}
.el-select {
  width: 100%;
}
.el-date-editor.el-input {
  width: 100%;
}
</style>
