import api from '../api/pointsSystem'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    points: [],
    pagination: '',
    response: []
  },
  getters: {
    points: state => state.points,
    pagination: state => state.pagination,
    response: state => state.response
  },
  mutations: {
    [types.mutations.pointsSystem.setPoints] (state, payload) {
      if (payload.data === undefined) {
        state.points = payload
        state.pagination = payload
      } else {
        state.points = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.pointsSystem.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.pointsSystem.getPoints] ({ commit }, payload) {
      try {
        const response = await api.getPoints(payload)
        commit(types.mutations.pointsSystem.setPoints, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.pointsSystem.addPoint] ({ commit }, payload) {
      try {
        const response = await api.addPoint(payload)
        commit(types.mutations.pointsSystem.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.pointsSystem.updatePoint] (state, payload) {
      try {
        await api.updatePoint(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.pointsSystem.deletePoint] (state, payload) {
      try {
        await api.deletePoint(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
