export default {
  getCustomerGroup (payload = {}) {
    const page = payload.page !== null ? `?page=${payload.page}` : ''
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/customer-group' + page)
  },
  addCustomerGroup (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/customer-group', payload)
  },
  updateCustomerGroup (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/customer-group/${payload.id}`, payload)
  },
  deleteCustomerGroup (payload = {}) {
    return axios.delete(process.env.VUE_APP_API_ENDPOINT + `/customer-group/${payload.id}`, payload)
  },
  getCustomerGroupById (payload) {
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/show-customer-group?customer_group_id=' + payload.customer_group_id)
  }
}
