import api from '../api/customerGroup'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    customerGroups: [],
    pagination: '',
    response: [],
    selectedCustomerGroupPoints: [],
    customerGroupById: [],
    customerGroupPointSystem: []
  },
  getters: {
    customerGroups: state => state.customerGroups,
    selectedCustomerGroupPoints: state => state.selectedCustomerGroupPoints,
    pagination: state => state.pagination,
    response: state => state.response,
    customerGroupById: state => state.customerGroupById,
    customerGroupPointSystem: state => state.customerGroupPointSystem
  },
  mutations: {
    [types.mutations.customerGroup.setCustomerGroup] (state, payload) {
      if (payload.data === undefined) {
        state.customerGroups = payload
        state.pagination = payload
      } else {
        state.customerGroups = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.customerGroup.setCustomerGroupPoints] (state, payload) {
      if (payload.data === undefined) {
        state.selectedCustomerGroupPoints = payload
        state.pagination = payload
      } else {
        state.selectedCustomerGroupPoints = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.customerGroup.setResponse] (state, payload) {
      state.response = payload
    },
    [types.mutations.customerGroup.setCustomerGroupById] (state, payload) {
      if (payload) {
        state.customerGroupById = payload
      }
      payload ? state.customerGroupPointSystem = payload[0].point_system : state.customerGroupPointSystem = {}
    }
  },
  actions: {
    async [types.actions.customerGroup.getCustomerGroupById] ({ commit }, payload) {
      try {
        const response = await api.getCustomerGroupById(payload)
        commit(types.mutations.customerGroup.setCustomerGroupById, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customerGroup.getCustomerGroup] ({ commit }, payload) {
      try {
        const response = await api.getCustomerGroup(payload)
        commit(types.mutations.customerGroup.setCustomerGroup, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customerGroup.getCustomerGroupPoints] ({ commit }, payload) {
      try {
        const response = await api.getCustomerGroup(payload)
        commit(types.mutations.customerGroup.setCustomerGroupPoints, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customerGroup.addCustomerGroup] ({ commit }, payload) {
      try {
        const response = await api.addCustomerGroup(payload)
        commit(types.mutations.customerGroup.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customerGroup.updateCustomerGroup] (state, payload) {
      try {
        await api.updateCustomerGroup(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.customerGroup.deleteCustomerGroup] (state, payload) {
      try {
        await api.deleteCustomerGroup(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
