<script>
import { mapGetters } from 'vuex'
export default {
  name: 'SideBarMenu',
  data () {
    const item = {
      date: '2016-05-02',
      name: 'Tom',
      address: 'No. 189, Grove St, Los Angeles'
    }
    return {
      tableData: Array(20).fill(item),
      activeIndex: '1',
      activeIndex2: '1',
      fourCourt: true,
      sic: true,
      adminUser: true
      // isLogin: false
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      // console.log(key, keyPath)
    },
    logout () {
      window.localStorage.removeItem('vuex')
      this.$store.commit('appUI/setIsLoading', false)
      this.$store.commit('appUI/setIsDisable', false)
      this.$notify({
        title: 'Success',
        message: 'Logged Out Successfully!',
        type: 'success',
        duration: 2000
      })
      this.$store.commit('customerGroup/setCustomerGroupById')
      this.$store.commit('appUI/setIsLoading', false)
      this.$store.commit('appUI/setIsDisable', false)
      this.$store.commit('auth/setIsLogin', false)
    },
    userPermission () {
      const userRole = this.user.roles.data[0].name
      if (userRole === 'sic') {
        this.sic = true
        this.fourCourt = true
        this.adminUser = false
      } else if (userRole === 'admin') {
        this.adminUser = true
        this.fourCourt = true
        this.sic = true
      } else {
        this.fourCourt = true
        this.sic = false
        this.adminUser = false
        this.$router.push('/transactions')
      }
    }
  },
  computed: {
    ...mapGetters('auth', {
      user: 'currentUser'
    })
  },
  created () {
    this.userPermission()
  }
}
</script>
