export default {
  getTransactions (payload) {
    const page = payload.page !== null ? `&page=${payload.page}` : ''
    if (payload.customer_id === 0 && payload.from_date === 0 && payload.dateOnly === 0 && payload.shift === 0) {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/transaction?filter_by_customer=' + payload.customer_id + page + '&filter_by_shift=' + payload.shift + '&export=' + payload.export)
    } else if (payload.from_date !== 0 && payload.to_date !== 0) {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/transaction?filter_by_customer=' + payload.customer_id + page + '&from_date=' + payload.from_date + '&to_date=' + payload.to_date + '&filter_by_shift=' + payload.shift + '&export=' + payload.export)
    } else {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/transaction?filter_by_customer=' + payload.customer_id + page + '&filter_customer_date=' + payload.dateOnly + '&filter_by_shift=' + payload.shift + '&export=' + payload.export)
    }
  },
  getTransactionByCustomer (payload) {
    if (payload.from_date === 0 && payload.dateOnly === 0 && payload.selectedcustomerGroup === 0 && payload.selectedProduct === 0 && payload.shift === 0) {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/transaction-by-customer?export=' + payload.export)
    } else if (payload.from_date !== 0 && payload.to_date !== 0) {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/transaction-by-customer?from_date=' + payload.from_date + '&to_date=' + payload.to_date + '&filter_by_customer_group=' + payload.selectedcustomerGroup + '&filter_by_product=' + payload.selectedProduct + '&filter_by_shift=' + payload.shift + '&export=' + payload.export)
    } else {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/transaction-by-customer?filter_customer_date=' + payload.dateOnly + '&filter_by_shift=' + payload.shift + '&filter_by_customer_group=' + payload.selectedcustomerGroup + '&filter_by_product=' + payload.selectedProduct + '&export=' + payload.export)
    }
  },
  addTransaction (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/transaction', payload)
  },
  getCustomerByBarcode (payload) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/customer-barcode', payload)
  }
}
