export default {
  login (payload) {
    return axios.post(process.env.VUE_APP_DOMAIN_ENDPOINT + '/oauth/token', payload)
  },
  exportDatabase () {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/export-database')
  },
  create () {},
  update () {},
  delete () {},
  getAuthUser (payload) {
    const AuthStr = 'Bearer ' + payload
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/auth/user?include=roles', { headers: { Authorization: AuthStr } })
  }
}
