export default {
  getProducts (payload = {}) {
    const page = payload.page !== null ? `?page=${payload.page}` : ''
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/product' + page)
  },
  addProduct (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/product', payload)
  },
  updateProduct (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/product/${payload.id}`, payload)
  },
  deleteProduct (payload = {}) {
    return axios.delete(process.env.VUE_APP_API_ENDPOINT + `/product/${payload.id}`, payload)
  },
  getDailyPoints (payload) {
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/daily-points?shift=' + payload.shift)
  }
}
