<template>
  <div>
    <div v-show="transact">
      <fieldset>
        <legend><strong>Customer Info</strong></legend>
        <el-row :gutter="18">
          <el-col :span="12">
            <h1>Tap Your Card Now</h1>
            {{rfid}}
            <!-- <el-input @input="onInputBarcode" v-model="ruleForm.barcode" name="code" disabled class="code" placeholder="Barcode" v-validate="{required: true}"></el-input>
              <span class="text-error">{{ errors.first('code') }}</span> -->
          </el-col>
          <div v-if="isFiltered">
            <el-col :span="6">
              <div class="user-photo">
                 <img class="logo" v-show="displayResult.first_name" src="../../src/assets/no-photo.jpg" />
              </div>
            </el-col>
            <el-col :span="6" :data="filteredCustomer" v-loading="loading">
              <br>
              <b class="user-info">{{displayResult.first_name}}</b> <b class="user-info">{{displayResult.last_name}}</b><br><br>
              <b class="user-info">{{displayResult.barcode}}</b><br><br>
              <p v-show="displayResult.first_name">POINTS: </p>&nbsp; <b class="user-point">{{displayResult.points}}</b>
            </el-col>
          </div>
        </el-row>
    </fieldset>
    <fieldset v-show="showGroup">
      <legend><strong>Customer Group</strong></legend>
        <div v-if="isFiltered">
          <el-col :data="filteredCustomer" v-loading="loading">
            <b class="user-group">{{ displayResult.customer_group.name }}</b>
          </el-col>
        </div>
    </fieldset>
    <fieldset>
      <legend><strong>Purchase Info</strong></legend>
      <el-form label-width="120px" label-position="right" style="padding:20px" @submit.prevent="validateForm">
        <el-row :gutter="10">
          <el-col :span="12">
            <el-form-item label="Product">
              <el-select style="width: 100%" name="product" v-model="ruleForm.product_id" @change="populatePrice()" placeholder="Select" clearable v-validate= "'required'">
              <el-option
                  v-for="item in filteredCustomerGroup"
                  :key="item.id"
                  :label="item.product.product_name"
                  :value="item.product.id"
                  >
              </el-option>
              </el-select>
              <span class="text-error">{{ errors.first('product') }}</span>
            </el-form-item>
            <el-form-item label="Amount">
              <el-input name="Amount" v-model="ruleForm.amount" v-validate="'required'" type="number">
              </el-input>
              <span class="text-error">{{ errors.first('Amount') }}</span>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Unit Price">
              <el-input name="Price" placeholder="0.00" v-validate="{required: true, decimal: 5}" readonly v-model="ruleForm.price" type="number" class="liter">
                <template slot="prepend">Php</template>
                <template slot="append">/Liter</template>
              </el-input>
              <span class="text-error">{{ errors.first('Price') }}</span>
            </el-form-item>
            <el-row :gutter="24">
              <el-col :span="12">
                <el-form-item label="Liters">
                  <el-input name="Liters" v-model="ruleForm.liters" v-validate="'required'" readonly type="number"></el-input>
                  <span class="text-error">{{ errors.first('Liters') }}</span>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="Points/Liter">
                  <el-input name="Liters" v-model="ruleForm.points" v-validate="'required'" readonly type="number"></el-input>
                  <span class="text-error">{{ errors.first('Liters') }}</span>
                </el-form-item>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label="Remarks">
            <el-col :span="24">
            <el-input placeholder="Remarks" v-model="ruleForm.remarks" name="remarks" style="float:center"></el-input></el-col>
          </el-form-item>
        </el-row>
        <br>
        <el-form-item class="pull-left">
          <el-button icon="fa fa-info-circle" index="/reports_summary/customer_points" :disabled="isSuccess" @click="customerPoints()" class="submit">&nbsp;View Customer Data</el-button>
        </el-form-item>
        <el-form-item class="pull-right">
          <el-button type="submit" class="submit" @click="submitForm" :disabled="isSuccess" icon="fa fa-check-circle">&nbsp;Save</el-button>
          <el-button class="colorPrimary" icon="fa fa-times-circle" v-on:click="clear_form()">&nbsp;Clear</el-button>
        </el-form-item>
      </el-form>
    </fieldset>
    </div>
    <div v-show="notify">
          <el-col :span="10" :offset="7">
            <el-card>
              <div slot="header">
                <br>
                <i class="fa fa-check-circle-o" style="margin-left: 39%;font-size: 80px;color: #51b156" aria-hidden="true"></i>
                <p style="margin-left: 35%;font-size: 21px;color: #51b156">SUCCESS</p>
              </div>
              <br>
              <div class="text item" style="margin-left: 8%;font-size: 17px;color: gray">
                <center>
                  <b class="note">Transaction Added Successfully!</b>
                  <br>
                  <b>You Earned</b><b style="font-size: 25px"> {{ customerCurrentPoint }} </b><b>Points</b>
                </center>
                <br><br><br>
                <el-col :span="12">
                  <el-button class="submit" @click="goList" style="margin-left: -35px;"><i class="fa fa-list">&nbsp;</i>Go to Transaction List</el-button>
                </el-col>
                <el-col :span="12">
                  <el-button type="success" @click="goBack" style="margin-left: -11px;"><i class="fa fa-plus">&nbsp;</i> Add New Transaction</el-button>
                </el-col>
                <br><br><br>
              </div>
            </el-card>
            <br><br><br>
          </el-col>
        </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import _ from 'lodash'

export default {
  name: 'Transactions',
  data () {
    return {
      transact: true,
      notify: false,
      showGroup: false,
      selectedProduct: '',
      selectPoints: '',
      customerGroupPoints: [],
      customerCurrentTransaction: [],
      customerCurrentPoint: '',
      loading: false,
      isPoints: false,
      barcodeInfo: [],
      imageUrl: '',
      liter_round: '',
      presyo: '',
      isFiltered: false,
      isSuccess: true,
      customer_group_id: '',
      ruleForm: {
        id: '',
        name: '',
        group: '',
        discount: '',
        barcode: '',
        expiry_date: '',
        sales_date: Date.now(),
        type: '',
        avail: '',
        points: '',
        product_id: '',
        price: '',
        amount: '',
        liters: '',
        remarks: ''
      },
      rfid: '',
      productInfo: [],
      save: true
    }
  },
  computed: {
    displayResult () {
      if (this.filteredCustomer.length === 1) {
        return this.filteredCustomer[0]
      }
      return ''
    },
    isChoosed: function () {
      if (this.form.type === '') {
        return true
      } else {
        return false
      }
    },
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('transaction', {
      filteredCustomer: 'transactionByBarcode'
    }),
    ...mapGetters('productManagement', {
      products: 'productsWithPoints'
    }),
    ...mapGetters('priceManagement', {
      prices: 'prices'
    }),
    ...mapGetters('auth', {
      shift: 'shift',
      user: 'currentUser'
    }),
    ...mapGetters('customerGroup', {
      filteredCustomerGroup: 'customerGroupPointSystem'
    }),
    ...mapGetters('pointsSystem', {
      points: 'points',
      pagination: 'pagination',
      response: 'response'
    })
  },
  created () {
    this.getCustomerGroup()
    this.getProducts()
    this.getPrices()
    this.getPoints()
    this.$barcodeScanner.init(this.onBarcodeScanned)
  },
  watch: {
    page () {
      this.getProducts()
      this.getPrices()
      this.getPoints()
    },
    'ruleForm.amount': function (val) {
      this.ruleForm.amount = val
      this.liter_round = val / this.ruleForm.price
      this.ruleForm.liters = this.liter_round.toFixed(2)
    },
    'rfid': function () { this.onInputBarcode() }
  },
  methods: {
    onInputBarcode: _.debounce(function () {
      // this.ruleForm.barcode = this.rfid
      alert('test' + this.rfid)
      // console.log(this.rfid)
      if (this.ruleForm.barcode !== '') {
        this.isFiltered = true
        this.loading = true
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.dispatch('transaction/getCustomerByBarcode', { barcode: 'mg-' + this.ruleForm.barcode })
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            if (this.filteredCustomer.length === 1) {
              this.loading = false
              this.isSuccess = false
              this.showGroup = true
              this.selectGroupPoints()
            } else {
              this.isSuccess = true
              this.$notify({
                title: 'Error',
                message: 'Barcode doesnt exists!',
                type: 'error',
                duration: 1000
              })
            }
            return ''
          })
          .catch(error => {
            this.$notify({
              title: 'Error',
              message: 'Somethig went wrong!',
              type: 'error',
              duration: 1000
            })
            throw new Error(error)
          })
      } else {
        this.isFiltered = false
      }
    }, 500),
    getProducts () {
      this.$store.dispatch('productManagement/getProducts', { page: null })
    },
    getPrices () {
      this.$store.dispatch('priceManagement/getPrices')
    },
    getPoints () {
      this.$store.dispatch('pointsSystem/getPoints', { page: null })
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup')
    },
    customerPoints () {
      this.$router.push('/reports_summary/customer_points')
      this.$store.dispatch('transaction/getTransactions', { customer_id: this.filteredCustomer[0].id, page: 1, from_date: 0, to_date: 0, dateOnly: 0 })
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          const form = {
            customer_id: this.filteredCustomer[0].id,
            product_id: this.ruleForm.product_id,
            price: this.ruleForm.price,
            amount: this.ruleForm.amount,
            liters: this.ruleForm.liters,
            points: this.ruleForm.liters * this.ruleForm.points,
            customer_group_id: this.filteredCustomer[0].customer_group_id,
            shift: this.shift,
            remarks: this.ruleForm.remarks,
            username: this.user.username
          }
          if (this.ruleForm.amount <= 10000) {
            this.createTransaction(form)
          } else {
            this.$notify({
              title: 'Error',
              message: 'Amount must not exceed within 10,000',
              type: 'error'
            })
          }
        }
      })
    },
    async createTransaction (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('transaction/addTransaction', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.customerCurrentTransaction = form
          this.transact = false
          this.notify = true
          this.customerCurrentPoint = (this.customerCurrentTransaction.points).toFixed(2)
          this.filteredCustomer[0].points = parseFloat(this.filteredCustomer[0].points) + parseFloat(this.customerCurrentPoint)
          this.clear_form()
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Transaction limit has exceeded for this shift!',
            type: 'error'
          })
          throw new Error(error)
        })
    },
    clear_form: function () {
      this.ruleForm = {
        barcode: '',
        product_id: '',
        amount: '',
        price: '',
        remarks: ''
      }
    },
    validateForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.$notify({
            title: 'Success',
            message: 'Transaction Saved!',
            type: 'success',
            duration: 2000
          })
          this.$nextTick(() => {
            this.$validator.reset()
          })
          this.clear_form()
        }
      })
    },
    populatePrice () {
      if (this.filteredCustomerGroup.length >= 0) {
        this.selectedProduct = this.filteredCustomerGroup.filter(el => el.product_id === this.ruleForm.product_id)
        this.ruleForm.price = this.selectedProduct[0].product.price.product_price
        this.ruleForm.points = this.selectedProduct[0].equivalent_points
      }
    },
    selectGroupPoints () {
      if (this.filteredCustomer.length >= 0) {
        this.$store.dispatch('customerGroup/getCustomerGroupById', { customer_group_id: this.filteredCustomer[0].customer_group_id })
      } else {
        this.filteredCustomerGroup = []
      }
    },
    goBack () {
      this.$router.push('/transactions')
      this.transact = true
      this.notify = false
    },
    goList () {
      this.$router.push('/reports_summary/customer_points')
    },
    onBarcodeScanned (barcode) {
      console.log(barcode)
      this.rfid = barcode
      // do someth  ing...
    },
    // Reset to the last barcode before hitting enter (whatever anything in the input box)
    resetBarcode () {
      let barcode = this.$barcodeScanner.getPreviousCode()
      console.log(barcode)
    }
  }
}
</script>

