<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <el-row :gutter="10">
      <el-col :span="10">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-plus"></i> Add Vehicle</span>
          </div>
          <el-form :model="ruleForm" label-width="90px">
          <el-form-item label="Name">
            <el-input name="Vehicle Name" :class="errors.has('Vehicle Name')? 'is-error':''" v-validate="'required'" v-model="ruleForm.vehicle_name" placeholder="Enter Vehicle Name"></el-input>
            <span v-show="errors.has('Vehicle Name')" class="text-error">{{ errors.first('Vehicle Name') }}</span>
          </el-form-item>
          <el-form-item label="Description">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
            placeholder="Enter Description"
            v-model="ruleForm.description">
          </el-input>
          </el-form-item>
            <el-form-item>
              <el-button style="float: right" v-if="isEdit" :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> UPDATE</el-button>
              <el-button style="float: right" v-else :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="14">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-list"></i> List of Vehicles</span>
          </div>
          <el-table
            :data="vehicles"
            style="width: 100%">
            <el-table-column
              prop="vehicle_name"
              label="Name"
              width="200">
            </el-table-column>
            <el-table-column
              prop="description"
              label="Description">
            </el-table-column>
            <el-table-column
              label="Action">
              <template slot-scope="scope">
                <el-button
                  icon="fa fa-pencil"
                  title="Edit Vehicle Information"
                  type="success"
                  circle
                  @click="handleEdit(scope.row)">
                </el-button>
                <el-button
                  icon="fa fa-trash"
                  title="Delete Vehicle Information"
                  class="colorPrimary"
                  circle
                  @click="handleDelete(scope.row)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
          </app-pagination>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>

import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import Modal from '@/components/Modal'

export default {
  data () {
    return {
      isEdit: false,
      ruleForm: {
        description: '',
        vehicle_name: ''
      },
      title: '',
      deleteDialog: false,
      vehicleInfo: [],
      page: 1
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('vehicleManagement', {
      vehicles: 'vehicles',
      pagination: 'pagination',
      response: 'response'
    })
  },
  watch: {
    page () {
      this.getVehicles()
    }
  },
  created () {
    this.getVehicles()
  },
  methods: {
    getVehicles () {
      const page = this.page
      this.$store.dispatch('vehicleManagement/getVehicles', { page })
    },
    clear_form () {
      this.ruleForm = {
        vehicle_name: '',
        description: ''
      }
      this.$nextTick(() => {
        this.$validator.reset()
      })
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updateVehicle(this.ruleForm)
          } else {
            this.addVehicle(this.ruleForm)
          }
        }
      })
    },
    handleEdit (item) {
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
    },
    handleDelete (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.vehicleInfo = Object.assign({}, item)
    },
    async addVehicle (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('vehicleManagement/addVehicle', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.vehicles.unshift(this.response)
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async updateVehicle (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('vehicleManagement/updateVehicle', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Successfully Updated!',
            type: 'success',
            duration: 2000
          })
          this.getVehicles()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('vehicleManagement/deleteVehicle', this.vehicleInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'SuccessFully Deleted!',
            type: 'success',
            duration: 2000
          })
          const index = this.vehicles.findIndex(value => value.id === this.vehicleInfo.id)
          this.vehicles.splice(index, 1)
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.deleteDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination,
    'app-modal': Modal
  }
}
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple {
  background: #99a9bf;
}
.clearfix {
  font-size: 18px;
}
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
</style>
