<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="16">
        <el-card class="box-card" shadow="never">
          <div slot="header" class="clearfix">
            <span id="span_header_1">Generate Summary</span>
          </div>
          <el-form :inline="true">
            <el-radio-group v-model="radio">
              <el-row>
                <el-form-item>
                  <el-radio :label="true">&nbsp;</el-radio>
                </el-form-item>
                <el-form-item style="padding: 0px 20px 0px 0px;" label="RANGE">
                  <el-date-picker
                    style="width: 116%"
                    v-model="dateFromTo"
                    :disabled="!radio"
                    @change="selectDateEvent"
                    type="daterange"
                    align="right"
                    start-placeholder="Start Date"
                    end-placeholder="End Date"
                    value-format="yyyy-MM-dd">
                  </el-date-picker>
                </el-form-item>
              </el-row>
              <el-row>
                <el-form-item>
                  <el-radio :label="false">&nbsp;</el-radio>
                </el-form-item>
                <el-form-item id="date" style="padding: 0px 10px 0px 0px;" label="DATE">
                  <el-date-picker
                    style="width: 201%;margin-left: 10px"
                    :disabled="radio"
                    v-model="dateOnly"
                    @change="selectDateOnly"
                    type="date"
                    value-format="yyyy-MM-dd"
                    placeholder="SELECT DATE">
                  </el-date-picker>
                </el-form-item>
              </el-row>
            </el-radio-group>
            <el-row>
              <br><br>
              <el-form-item label="FILTER BY SHIFT">
                  <el-select @change="selectShiftEvent" style="margin-left: 80px;width: 131%" clearable v-model="shift_number" placeholder="SELECT SHIFT">
                    <el-option label="Shift 1" value="1"></el-option>
                    <el-option label="Shift 2" value="2"></el-option>
                    <el-option label="Shift 3" value="3"></el-option>
                  </el-select>
                </el-form-item>
            </el-row>
            <el-row :gutter="20">
              <el-form-item :inline="true" id="select_custom_width" label="FILTER BY CUSTOMER GROUP">
                <el-select @change="selectGroupEvent" style="margin-left: -4px;"  clearable v-model="selectedcustomerGroup" placeholder="----- SELECT -----">
                <el-option
                  v-for="item in customerGroups"
                  :key="item.value"
                  :label="item.name"
                  :value="item.id">
                </el-option>
                </el-select>
              </el-form-item>
            </el-row>
            <el-row>
              <el-form-item :inline="true" id="filter_product" label="FILTER BY PRODUCT">
                <el-select @change="selectProductEvent" style="margin-left: 52px;"  clearable v-model="selectedProduct" placeholder="----- SELECT -----">
                  <el-option
                  v-for="item in products"
                  :key="item.value"
                  :label="item.product_name"
                  :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-row>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-row>
          <el-button @click="exportExcel" class="submit" style="width: 100%" icon="fa fa-download">
            EXPORT TO EXCEL &nbsp;
          </el-button>
        </el-row>
        <el-row>
          <el-card class="box-card" shadow="never">
            <template>
              <div slot="header" style="text-align: center;" class="clearfix">
                <span id="span_header_2">CUSTOMER GROUP: {{ selectedGroupName }}</span>
              </div>
            </template>
            <el-table
              :data="customerGroupPoints"
              border
              style="width: 100%">
              <el-table-column
                prop="product_id"
                label="PRODUCT">
              <template slot-scope="scope">
                {{ scope.row.product.product_name}}
              </template>
              </el-table-column>
              <el-table-column
                prop="equivalent_points"
                label="POINTS">
              </el-table-column>
            </el-table>
          </el-card>
        </el-row>
      </el-col>
    </el-row>
    <el-row>
      <el-card shadow="never">
        <el-tabs type="card" v-model="activeName">
          <el-tab-pane label="Transaction" name="transaction">
            <el-row>
              <el-table
                border
                :data="transactionCustomer"
                height="400"
                style="width: 100%">
                <el-table-column
                  fixed
                  width="130"
                  prop="created_at"
                  label="DATE">
                <template slot-scope="scope">
                  {{ toDateString(scope.row.created_at) }}
                </template>
                </el-table-column>
                <el-table-column
                  fixed
                  width="130"
                  prop="created_at"
                  label="ENCODED TIME">
                <template slot-scope="scope">
                  {{ toDateTimeString(scope.row.created_at) }}
                </template>
                </el-table-column>
                <el-table-column
                  fixed
                  width="180"
                  prop="customer_id"
                  label="CUSTOMER">
                <template slot-scope="scope">
                  {{ scope.row.customer.first_name + ' ' + scope.row.customer.last_name}}
                </template>
                </el-table-column>
                <el-table-column
                  width="150"
                  prop="customer_group_id"
                  label="GROUP">
                <template slot-scope="scope">
                  {{ scope.row.customer_group.name }}
                </template>
                </el-table-column>
                <el-table-column
                  width="140"
                  prop="vehicles"
                  label="VEHICLE">
                <template slot-scope="scope">
                  {{ scope.row.customer.vehicles }}
                </template>
                </el-table-column>
                <el-table-column
                  width="135"
                  prop="product_id"
                  label="PRODUCT">
                <template slot-scope="scope">
                  {{ scope.row.product.product_name }}
                </template>
                </el-table-column>
                <el-table-column
                  width="120"
                  prop="price"
                  label="UNIT PRICE">
                </el-table-column>
                <el-table-column
                  width="120"
                  prop="liters"
                  label="LTR/QTY">
                </el-table-column>
                <el-table-column
                  width="140"
                  prop="points"
                  label="EARNED POINTS">
                </el-table-column>
                <el-table-column
                  width="140"
                  prop="amount"
                  label="TOTAL AMOUNT">
                </el-table-column>
              </el-table>
            </el-row>
            <el-row>
              <el-card shadow="never">
                <el-row :gutter="10">
                  <el-form label-width="150px">
                    <el-col :span="12">
                      <el-form-item label="LITER/QTY">
                        <el-input readonly v-model="totalliters"></el-input>
                      </el-form-item>
                      <el-form-item label="EARNED POINTS">
                        <el-input readonly v-model="totalpoints"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="12">
                      <el-form-item label="TOTAL AMOUNT">
                        <el-input readonly v-model="totalamount"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-form>
                </el-row>
              </el-card>
            </el-row>
          </el-tab-pane>
          <!-- <el-tab-pane label="Customer" name="customer">Customer</el-tab-pane> -->
        </el-tabs>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'ViewAccumulatedPoints',
  data () {
    return {
      url: '',
      selectedGroupName: '',
      groupSelected: '',
      customerGroupPoints: [],
      selectPoints: '',
      selectedGroup: '',
      customer_group: '',
      selectedProduct: '',
      selectedcustomerGroup: '',
      shift_number: '',
      dateOnly: '',
      dateFromTo: '',
      labelPosition: 'right',
      total_amount: '',
      filter_customer_group: '',
      filter_product: '',
      radio: true,
      activeName: 'transaction',
      pointsCurrentPage: 1,
      exportValue: 1,
      fromDate: '',
      toDate: '',
      oneDate: '',
      shiftOnly: '',
      productOnly: '',
      groupOnly: ''
    }
  },
  computed: {
    totalamount: function () {
      if (this.transactionCustomer.length > 0) {
        const amountArr = this.transactionCustomer.map(el => parseFloat(el.amount))
        const reducer = (accumulator, currentValue) => accumulator + currentValue
        const finalAmount = amountArr.reduce(reducer).toFixed(2)
        return finalAmount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      }
      return 0
    },
    totalpoints: function () {
      if (this.transactionCustomer.length > 0) {
        const pointsArr = this.transactionCustomer.map(el => parseFloat(el.points))
        const reducer = (accumulator, currentValue) => accumulator + currentValue
        const finalPoints = pointsArr.reduce(reducer).toFixed(2)
        return finalPoints.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      }
      return 0
    },
    totalliters: function () {
      if (this.transactionCustomer.length > 0) {
        const litersArr = this.transactionCustomer.map(el => parseFloat(el.liters))
        const reducer = (accumulator, currentValue) => accumulator + currentValue
        const finalLiters = litersArr.reduce(reducer).toFixed(2)
        return finalLiters.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      }
      return 0
    },
    ...mapGetters('transaction', {
      transactions: 'transactions',
      transactionCustomer: 'transactionCustomer'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups',
      response: 'response',
      selectedCustomerGroupPoints: 'selectedCustomerGroupPoints'
    }),
    ...mapGetters('productManagement', {
      products: 'products'
    }),
    ...mapGetters('auth', {
      shift: 'shift'
    }),
    ...mapGetters('pointsSystem', {
      points: 'points'
    })
  },
  created () {
    this.getCustomerGroup()
    this.getProducts()
    this.getPoints()
    this.getTransactionByCustomer()
  },
  watch: {
    page () {
      this.getCustomerGroup()
      this.getselected()
      this.getProducts()
      this.getPoints()
    }
  },
  methods: {
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', { page: null })
    },
    getProducts () {
      this.$store.dispatch('productManagement/getProducts')
    },
    getPoints () {
      this.$store.dispatch('pointsSystem/getPoints', { page: this.pointsCurrentPage })
    },
    getTransactionByCustomer () {
      this.$store.dispatch('transaction/getTransactionByCustomer', {from_date: 0, dateOnly: 0, selectedcustomerGroup: 0, selectedProduct: 0, shift: 0, export: 0})
    },
    exportExcel () {
      this.fromDate = this.dateFromTo[0] ? this.dateFromTo[0] : 0
      this.toDate = this.dateFromTo[1] ? this.dateFromTo[1] : 0
      this.oneDate = this.dateOnly ? this.dateOnly : 0
      this.shiftOnly = this.shift_number ? this.shift_number : 0
      this.productOnly = this.selectedProduct ? this.selectedProduct : 0
      this.groupOnly = this.selectedcustomerGroup ? this.selectedcustomerGroup : 0
      if (this.fromDate !== 0 && this.toDate !== 0) {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/transaction-by-customer?from_date=' + this.fromDate + '&to_date=' + this.toDate + '&filter_by_customer_group=' + this.groupOnly + '&filter_by_product=' + this.productOnly + '&filter_by_shift=' + this.shiftOnly + '&export=' + this.exportValue
      } else if (this.oneDate !== 0) {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/transaction-by-customer?filter_customer_date=' + this.oneDate + '&filter_by_shift=' + this.shiftOnly + '&filter_by_customer_group=' + this.groupOnly + '&filter_by_product=' + this.productOnly + '&export=' + this.exportValue
      } else {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/transaction-by-customer?export=' + this.exportValue
      }
      window.location.href = this.url
    },
    selectDateEvent () {
      if (this.dateFromTo !== null) {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: this.dateFromTo[0],
          to_date: this.dateFromTo[1],
          shift: this.shift_number ? this.shift_number : 0,
          selectedcustomerGroup: this.selectedcustomerGroup ? this.selectedcustomerGroup : 0,
          selectedProduct: this.selectedProduct ? this.selectedProduct : 0,
          export: 0
        })
      } else {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: 0,
          dateOnly: 0,
          selectedcustomerGroup: 0,
          selectedProduct: 0,
          shift: 0,
          export: 0
        })
      }
    },
    selectDateOnly () {
      if (this.dateOnly !== null) {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: 0,
          to_date: 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.shift_number ? this.shift_number : 0,
          selectedcustomerGroup: this.selectedcustomerGroup ? this.selectedcustomerGroup : 0,
          selectedProduct: this.selectedProduct ? this.selectedProduct : 0,
          export: 0
        })
      } else {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: 0,
          selectedcustomerGroup: 0,
          selectedProduct: 0,
          dateOnly: 0,
          shift: 0,
          export: 0
        })
      }
    },
    selectShiftEvent () {
      if (this.shift_number !== null) {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.shift_number,
          selectedcustomerGroup: this.selectedcustomerGroup ? this.selectedcustomerGroup : 0,
          selectedProduct: this.selectedProduct ? this.selectedProduct : 0,
          export: 0
        })
      } else {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: 0,
          selectedcustomerGroup: 0,
          selectedProduct: 0,
          dateOnly: 0,
          shift: 0,
          export: 0
        })
      }
    },
    selectGroupEvent () {
      if (this.selectedcustomerGroup !== null) {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.shift_number ? this.shift_number : 0,
          selectedcustomerGroup: this.selectedcustomerGroup,
          selectedProduct: this.selectedProduct ? this.selectedProduct : 0,
          export: 0
        })
        this.selectGroup()
        this.selectGroupPoints()
      } else {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: 0,
          dateOnly: 0,
          selectedcustomerGroup: 0,
          export: 0
        })
      }
    },
    selectProductEvent () {
      if (this.selectedProduct !== null) {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.shift_number ? this.shift_number : 0,
          selectedcustomerGroup: this.selectedcustomerGroup ? this.selectedcustomerGroup : 0,
          selectedProduct: this.selectedProduct,
          export: 0
        })
      } else {
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: 0,
          dateOnly: 0,
          selectedcustomerGroup: 0,
          selectedProduct: 0,
          export: 0
        })
      }
    },
    selectGroup () {
      if (this.selectedcustomerGroup !== '') {
        this.groupSelected = this.customerGroups.filter(value => value.id === this.selectedcustomerGroup)
        this.selectedGroup = this.groupSelected.find(el => el.name)
        this.selectedGroupName = this.selectedGroup.name
        this.$store.dispatch('transaction/getTransactionByCustomer', {
          from_date: this.dateFromTo[0] ? this.dateFromTo[0] : 0,
          to_date: this.dateFromTo[1] ? this.dateFromTo[1] : 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.shift_number ? this.shift_number : 0,
          selectedcustomerGroup: this.selectedcustomerGroup,
          selectedProduct: 0,
          export: 0
        })
      } else {
        this.selectedGroupName = ''
      }
    },
    selectGroupPoints () {
      if (this.selectedcustomerGroup !== '') {
        this.selectPoints = this.customerGroups.filter(el => el.id === this.selectedcustomerGroup)
        this.customerGroupPoints = this.selectPoints[0].point_system
      } else {
        this.customerGroupPoints = []
      }
    }
  }
}
</script>

