import api from '../api/vehicleManagement'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    vehicles: [],
    pagination: '',
    response: []
  },
  getters: {
    vehicles: state => state.vehicles,
    pagination: state => state.pagination,
    response: state => state.response
  },
  mutations: {
    [types.mutations.vehicleManagement.setVehicles] (state, payload) {
      if (payload.data === undefined) {
        state.vehicles = payload
        state.pagination = payload
      } else {
        state.vehicles = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.vehicleManagement.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.vehicleManagement.getVehicles] ({ commit }, payload) {
      try {
        const response = await api.getVehicles(payload)
        commit(types.mutations.vehicleManagement.setVehicles, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.vehicleManagement.addVehicle] ({ commit }, payload) {
      try {
        const response = await api.addVehicle(payload)
        commit(types.mutations.vehicleManagement.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.vehicleManagement.updateVehicle] (state, payload) {
      try {
        await api.updateVehicle(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.vehicleManagement.deleteVehicle] (state, payload) {
      try {
        await api.deleteVehicle(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
