<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <el-button @click="exportProducts" class="submit" style="width: 23%;float: right" icon="fa fa-download">
      EXPORT TO EXCEL &nbsp;
    </el-button>
    <br><br><br>
    <el-row :gutter="10">
      <el-col :span="10">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-plus"></i> New Product</span>
          </div>
          <el-form :model="ruleForm" label-width="130px">
            <el-form-item label="PRODUCT NAME">
              <el-input name="Product_Name" :class="errors.has('Product_Name')? 'is-error':''" v-validate="'required'" v-model="ruleForm.product_name" placeholder="Enter Product Name"></el-input>
            <span v-show="errors.has('Product_Name')" class="text-error">{{ errors.first('Product_Name') }}</span>
            </el-form-item>
            <el-form-item label="SELECT TYPE">
              <el-radio-group v-model="ruleForm.product_type" v-validate="'required'" name="Product_type">
                <el-radio label="FUEL"></el-radio>
                <el-radio label="LUBES"></el-radio>
              </el-radio-group><br>
               <span class="text-error">{{ errors.first('Type') }}</span>
            </el-form-item>
            <el-form-item>
              <el-button style="float: right" v-if="isEdit" :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> UPDATE</el-button>
              <el-button style="float: right" v-else :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="14">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-list"></i> Product List</span>
          </div>
          <el-table
            :data="products"
            border
            style="width: 100%">
            <el-table-column
              prop="product_name"
              label="PRODUCT NAME">
            </el-table-column>
            <el-table-column
              prop="product_type"
              label="PRODUCT TYPE">
            </el-table-column>
            <el-table-column
              label="Action">
              <template slot-scope="scope">
                <el-button
                  icon="fa fa-pencil"
                  title="Edit Customer Information"
                  type="success"
                  circle
                  @click="handleEdit(scope.row)">
                </el-button>
                <el-button
                  v-show="productDisplay"
                  icon="fa fa-trash"
                  title="Delete Customer Information"
                  class="colorPrimary"
                  circle
                  @click="handleDelete(scope.row)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
         <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
         </app-pagination>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>

import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import Modal from '@/components/Modal'

export default {
  data () {
    return {
      productDisplay: false,
      isEdit: false,
      ruleForm: {
        product_name: '',
        product_type: 'FUEL'
      },
      title: '',
      deleteDialog: false,
      productInfo: [],
      page: 1,
      exportValue: 1
    }
  },
  computed: {
    ...mapGetters('user', {
      users: 'users'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('productManagement', {
      products: 'products',
      pagination: 'pagination',
      response: 'response'
    })
  },
  watch: {
    page () {
      this.getProducts()
    }
  },
  created () {
    this.getProducts()
  },
  methods: {
    exportProducts () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/product?export=' + this.exportValue
      window.location.href = this.url
    },
    getProducts () {
      const page = this.page
      this.$store.dispatch('productManagement/getProducts', { page })
    },
    clear_form: function () {
      this.ruleForm = {
        product_name: '',
        product_type: ''
      }
      this.$nextTick(() => {
        this.$validator.reset()
      })
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updateProduct(this.ruleForm)
          } else {
            this.addProduct(this.ruleForm)
          }
        }
      })
    },
    handleEdit (item) {
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
    },
    handleDelete (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.productInfo = Object.assign({}, item)
    },
    async addProduct (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('productManagement/addProduct', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Product Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.products.unshift(this.response)
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async updateProduct (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('productManagement/updateProduct', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Product Successfully Updated!',
            type: 'success',
            duration: 2000
          })
          this.getProducts()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('productManagement/deleteProduct', this.productInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Product SuccessFully Deleted!',
            type: 'success',
            duration: 2000
          })
          const index = this.products.findIndex(value => value.id === this.productInfo.id)
          this.products.splice(index, 1)
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.deleteDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination,
    'app-modal': Modal
  }
}
</script>

<style scoped>
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple {
  background: #99a9bf;
}
.clearfix {
  font-size: 18px;
}
</style>
