import types from '../../types'

export default {
  namespaced: true,
  state: {
    isLoading: false,
    isDisable: false
  },
  getters: {
    isLoading: state => state.isLoading,
    isDisable: state => state.isDisable
  },
  mutations: {
    [types.mutations.appUI.setIsLoading] (state, payload) {
      state.isLoading = payload
    },
    [types.mutations.appUI.setIsDisable] (state, payload) {
      state.isDisable = payload
    }
  },
  actions: {
  }
}
