<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="12">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-list"></i> List of User</span>
          </div>
          <el-table
            :data="users"
            border
            style="width: 100%">
            <el-table-column
              prop="username"
              label="USERNAME"
               width="150">
            </el-table-column>
            <el-table-column
            label="ROLE TYPE">
            <template slot-scope="scope">
              {{ scope.row.roles.data[0].display_name }}
            </template>
            </el-table-column>
            <el-table-column
              width="180"
              prop="password"
              label="CHANGE PASSWORD">
            <template slot-scope="scope">
            <el-button
            icon="fa fa-pencil"
            title="Edit Customer Information"
            type="success"
            @click="handleEdit(scope.row)"
            circle >
          </el-button>
          </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    <el-col :span="12">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-lock"></i> Change Password</span>
          </div>
          <el-form :model="form" label-position="left" label-width="120px">
          <el-form-item label="UserName">
            <el-input name="Name" :class="errors.has('Name')? 'is-error':''" v-validate="'required'" v-model="ruleForm.username" placeholder="Enter Name"></el-input>
            <span v-show="errors.has('Name')" class="text-error">{{ errors.first('Name') }}</span>
          </el-form-item>
          <el-form-item label="Old Password">
            <el-input name="Old Password" type="password" :class="errors.has('Old Password')? 'is-error':''" v-validate="{ required: true, min:6 }" minlength="6" v-model="ruleForm.old_password" placeholder="Old Password"></el-input>
            <span v-show="errors.has('Old Password')" class="text-error">{{ errors.first('Old Password') }}</span>
          </el-form-item>
          <el-form-item label="New Password">
            <el-input name="Password" type="password" :class="errors.has('Password')? 'is-error':''" ref="password" v-validate="{ required: true, min:6 }" minlength="6" v-model="ruleForm.password" placeholder="New Password"></el-input>
            <span v-show="errors.has('Password')" class="text-error">{{ errors.first('Password') }}</span>
          </el-form-item>
          <el-form-item label="Confirm">
            <el-input name="Confirm Password" v-validate="'required|confirmed:password'" minlength="6" type="password" v-model="ruleForm.confirm_password" placeholder="Confirm Password"></el-input>
            <span v-show="errors.has('Confirm Password')" class="text-error">{{ errors.first('Confirm Password') }}</span>
          </el-form-item>
            <el-form-item>
              <el-button style="float: right" class="submit" icon="fa fa-floppy-o" @click="submit"> CHANGE </el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      isShow: true,
      isEdit: false,
      ruleForm: {
        id: '',
        old_password: '',
        password: '',
        username: '',
        confirm_password: ''
      },
      form: {
        name: '',
        resource: ''
      },
      tableData: []
    }
  },
  computed: {
    ...mapGetters({
      users: 'user/users',
      roles: 'user/roles',
      response: 'user/response'
    })
  },
  created () {
    this.getRoles()
    this.getUsers()
  },
  methods: {
    getRoles () {
      this.$store.dispatch('user/getRoles')
    },
    getUsers () {
      this.$store.dispatch('user/getUsers')
    },
    submit () {
      const form = {
        id: this.ruleForm.id,
        old_password: this.ruleForm.old_password,
        new_password: this.ruleForm.password,
        confirm_password: this.ruleForm.confirm_password
      }
      this.updateUser(form)
    },
    async updateUser (form) {
      this.$store.dispatch('user/updateUserPassword', form)
        .then(() => {
          this.$notify({
            title: 'Success',
            message: 'User Password Successfully Updated!',
            type: 'success',
            duration: 2000
          })
        })
        .catch(error => {
          this.$notify({
            title: 'Error',
            message: 'Old Password is Incorrect!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    clear_form: function () {
      this.ruleForm = {
        username: '',
        password: '',
        confirm_password: ''
      }
    },
    validateForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.$notify({
            title: 'Success',
            message: 'Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.$nextTick(() => {
            this.$validator.reset()
          })
          this.clear_form()
        }
      })
    },
    handleEdit (item) {
      this.isShow = false
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
      this.ruleForm.id = item.id
      console.log(item)
    }
  },
  components: {
    'app-pagination': Pagination
  }
}
</script>

<style>
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple {
  background: #99a9bf;
}
.clearfix {
  font-size: 18px;
}
</style>
