<template>
    <div>
      <h2 style="text-align: center">EXPORT TO EXCEL OR DATABASE</h2>
      <hr>
      <br><br><br>
      <el-row :gutter="24">
        <el-col :span="14">
          <el-form>
            <el-select class="input select" style="width: 88%;margin-left: 37px;" clearable v-model="selectExport" placeholder="----- SELECT -----">
              <el-option label="Backup(Sql File).sql" value="database"></el-option>
              <el-option label="Price Update Summaries" value="priceExport"></el-option>
              <el-option label="Points Update Summaries" value="pointsExport"></el-option>
              <el-option label="Customer Registration Logs" value="customerExport"></el-option>
              <el-option label="Transaction Logs" value="transactionExport"></el-option>
              <el-option label="Redeem Logs" value="redeemExport"></el-option>
            </el-select>
          </el-form>
        </el-col>
        <el-col :span="8">
          <a v-on:click='exportNow' style="cursor: pointer">
            <el-card shadow="hover" id="tile" class="tile-yellow">
              <h1 style="text-align: center;"><i class="fa fa-download"></i>&nbsp;&nbsp;<b>EXPORT NOW</b></h1>
            </el-card>
          </a>
        </el-col>
      </el-row>
      <br><br><br>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      url: '',
      selectExport: ''
    }
  },
  computed: {
    ...mapGetters('databaseManagement', {
      exportDatabase: 'exportDatabase',
      response: 'response'
    })
  },
  watch: {
    page () {
      this.getDatabases()
    }
  },
  created () {
    this.getDatabases()
  },
  methods: {
    exportNow () {
      switch (this.selectExport) {
        case 'database':
          this.handleDownload()
          break
        case 'priceExport':
          this.exportPrice()
          break
        case 'pointsExport':
          this.exportPoints()
          break
        case 'customerExport':
          this.exportCustomer()
          break
        case 'transactionExport':
          this.exportTransaction()
          break
        case 'redeemExport':
          this.exportRedeem()
          break
      }
    },
    getDatabases () {
      this.$store.dispatch('databaseManagement/getDatabases', {page: null})
    },
    handleDownload () {
      this.url = this.exportDatabase[0].path
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Database Successfully Exported!',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportPrice () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/export-price-summary'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Price Update Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportPoints () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/export-point-system-summary'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Points Update Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportCustomer () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/customer-logs'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Customer Logs Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportTransaction () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/transaction-logs'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Transaction Logs Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportRedeem () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/redeem-logs'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Redeem Logs Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    clearForm () {
      this.selectExport = ''
    }
  }
}
</script>
