<template>
  <div>
    <el-card>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/customers/list' }">Customer List</el-breadcrumb-item>
      <el-breadcrumb-item>Customer Update</el-breadcrumb-item>
    </el-breadcrumb>
    </el-card>
    <br><br>
    <el-row>
      <el-col :span="24">
          <p style="font-size: 20px"><i class="fa fa-pencil"></i> Customer Update</p>
      </el-col>
    </el-row>
    <hr>
    <el-row :gutter="20">
      <el-form :model="ruleForm" label-position="left" ref="ruleForm" label-width="150px" class="demo-ruleForm">
        <el-col :span="12">
          <div style="margin-bottom: 22px;">
          <el-form-item label="Barcode">
            <el-input style="margin-bottom: 15px;" :name="ruleForm.selectPlateNumberOrBarcode? 'Barcode':'Plate Number'" :class="errors.has('Barcode')||errors.has('Plate Number')? 'is-error':''" v-validate="'required'" v-model="customer.barcode" :placeholder="isPlateNumberOrBarcode? 'Barcode':'Plate Number'"></el-input>
            <span style="margin-left: 41%;" v-show="errors.has('Barcode')||errors.has('Plate Number')" class="text-error">This field is required</span>
          </el-form-item>
          </div>
          <el-form-item label="First Name">
            <el-input name="First Name" :class="errors.has('First Name')? 'is-error':''" v-validate="'required'" v-model="customer.first_name" placeholder="Enter First Name"></el-input>
            <span v-show="errors.has('First Name')" class="text-error">{{ errors.first('First Name') }}</span>
          </el-form-item>
          <el-form-item label="Middle Initial" prop="middleInitial">
            <el-input placeholder="M.I" maxlength="1" style="width: 15%;" v-model="customer.middle_initial"></el-input>
          </el-form-item>
          <el-form-item label="Last Name">
            <el-input name="Last Name" :class="errors.has('Last Name')? 'is-error':''" v-validate="'required'" v-model="customer.last_name" placeholder="Enter Last Name"></el-input>
            <span v-show="errors.has('Last Name')" class="text-error">{{ errors.first('Last Name') }}</span>
          </el-form-item>
          <el-form-item label="Birthday">
            <el-date-picker name="Birthday" :class="errors.has('Birthday')? 'is-error':''" v-validate="'required'" type="date" placeholder="Pick a date" v-model="customer.birthdate" style="width: 100%;"></el-date-picker>
            <span v-show="errors.has('Birthday')" class="text-error">{{ errors.first('Birthday') }}</span>
          </el-form-item>
          <el-form-item label="Address">
            <el-input name="city" :class="errors.has('city')? 'is-error':''" v-validate="'required'" title="CITY/MUNICIPALITY" placeholder="BRGY/CITY/PROVINCE" v-model="customer.address"></el-input>
            <span v-show="errors.has('city')" class="text-error">This field is required</span>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="Contact Number">
            <el-input name="Contact Number" :class="errors.has('Contact Number')? 'is-error':''" v-validate="{ required: true, min:11, length:11, regex: /^([0-9]+)$/ }" v-model="customer.mobile_no" maxlength="11"></el-input>
            <span v-show="errors.has('Contact Number')" class="text-error">{{ errors.first('Contact Number') }}</span>
          </el-form-item>
          <el-form-item label="Reference No.">
            <!-- v-validate="'required|email'" -->
            <el-input name="Reference No." :class="errors.has('Reference No.')? 'is-error':''" v-model="customer.reference_no"></el-input>
            <span v-show="errors.has('Reference No.')" class="text-error">{{ errors.first('Reference No.') }}</span>
          </el-form-item>
          <el-form-item label="Vehicles">
            <!-- v-validate="'required'" -->
            <el-select name="Vehicles" multiple :class="errors.has('Vehicles')? 'is-error':''" v-validate="'required'" v-model="customerVehic">
              <el-option
                v-for="item in vehicle"
                :key="item.id"
                :label="item.vehicle_name"
                :value="item.vehicle_name">
              </el-option>
            </el-select>
            <span v-show="errors.has('Vehicles')" class="text-error">{{ errors.first('Vehicles') }}</span>
          </el-form-item>
          <el-form-item label="Customer Group">
            <!-- v-validate="'required'" -->
            <el-select name="Customer Group" :class="errors.has('Customer Group')? 'is-error':''" v-model="customer.customer_group_id">
              <el-option
                v-for="item in customerGroups"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <span v-show="errors.has('Customer Group')" class="text-error">{{ errors.first('Customer Group') }}</span>
          </el-form-item>
          <el-form-item>
            <el-button class="submit" round @click="submitForm" :loading="isLoading" :disabled="isDisable"><i class="fa fa-check"></i> Submit</el-button>
            <el-button class="colorPrimary" round @click="resetForm('customer')"><i class="fa fa-undo"></i> Reset</el-button>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
import moment from 'moment'

export default {
  data () {
    return {
      customerVehic: [],
      customerVehicle: [],
      room_number: '',
      lot_number: '',
      street_name: '',
      barangay: '',
      city: '',
      province: '',
      selectPlateNumberOrBarcode: '',
      ruleForm: {
        barcode: '',
        first_name: '',
        middleInitial: '',
        last_name: '',
        birthdate: '',
        address: '',
        mobile_no: '',
        expire_at: moment().add(30, 'days').format('YYYY-MM-DD'),
        reference: ''
        // selectGroup: '',
        // customerType: ''
      }
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('customer', {
      customer: 'customer'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups'
    }),
    ...mapGetters('vehicleManagement', {
      vehicle: 'vehicles'
    }),
    ...mapGetters('auth', {
      user: 'currentUser',
      shift: 'shift'
    }),
    isPlateNumberOrBarcode () {
      if (this.ruleForm.selectPlateNumberOrBarcode === 'Barcode') {
        return true
      } else {
        return false
      }
    }
  },
  created () {
    this.getVehicles()
    this.vehicleCustomer()
  },
  methods: {
    vehicleCustomer () {
      let vehic = this.customer.vehicles.split(',')
      this.customerVehicle.push(vehic)
      this.customerVehic = this.customerVehicle[0]
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', {page: null})
    },
    getVehicles () {
      this.$store.dispatch('vehicleManagement/getVehicles', {page: null})
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          const form = {
            id: this.customer.id,
            barcode: this.customer.barcode,
            first_name: this.customer.first_name,
            last_name: this.customer.last_name,
            birthdate: moment(this.customer.birthdate).format('YYYY-MM-DD'),
            address: this.customer.address,
            mobile_no: this.customer.mobile_no,
            customer_group_id: this.customer.customer_group_id,
            vehicles: (this.customerVehic).join(','),
            expire_at: this.customer.expire_at,
            middle_initial: this.customer.middle_initial,
            shift: this.shift,
            username: this.user.username,
            reference_no: this.customer.reference_no
          }
          this.updateCustomer(form)
        }
      })
    },
    async updateCustomer (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('customer/updateCustomer', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Updated Successfully!',
            type: 'success'
          })
          this.resetForm()
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
    },
    resetForm () {
      this.customer.barcode = ''
      this.customer.first_name = ''
      this.customer.last_name = ''
      this.customer.birthdate = ''
      this.customer.address = ''
      this.customer.mobile_no = ''
      this.customer.customer_group_id = ''
      this.customerVehic = ''
      this.customer.middle_initial = ''
      this.$nextTick(() => this.$validator.reset())
    }
  }
}
</script>

