      :dialogVisible="redeemDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit"
      >
      <div slot="body">
        <el-form status-icon label-width="120px" >
          <center><b class="title">ACCUMULATED POINTS AND LITERS TODAY</b></center>
          <hr><br>
          <el-row :gutter="24">
            <el-col :span="12">
              <el-form>
                <span class="demo-input-label" style="width: auto;">Select Shift: &nbsp;</span>
                <el-select @change="selectShiftEvent" clearable v-model="selectShift" placeholder="---SELECT ---">
                  <el-option label="Shift 1" value="1"></el-option>
                  <el-option label="Shift 2" value="2"></el-option>
                  <el-option label="Shift 3" value="3"></el-option>
                </el-select>
              </el-form>
            </el-col>
            <el-col :span="12">
              <p class="date">DATE: <b class="date_decoration"> {{ date | moment }} </b></p>
            </el-col>
          </el-row>
          <br>
          <el-table
            :data="dailyPoints"
            border
            style="width: 100%">
            <el-table-column
              prop="product_name"
              label="PRODUCT">
            </el-table-column>
            <el-table-column
              prop="daily_points"
              label="POINTS AVAILED">
            </el-table-column>
            <el-table-column
              prop="daily_liters"
              label="TOTAL LITERS">
            </el-table-column>
          </el-table>
        </el-form>
      </div>
    </app-modal>
    <transition name="fade">
      <el-row :gutter="10">
        <el-col :span="15">
          <el-card shadow="hover" id="tile" style="height: 45px;margin-top: 11px;" class="tile-black">
            <el-row :gutter="14" style="margin-top: -5px;">
              <el-col :span="8">
                <b style="color: white; font-size: 15px;font-weight: 600;margin-left: 4px">STATION:&nbsp;{{ selectedBranch }} Branch</b>
              </el-col>
              <el-col :span="8">
                <b style="color: white; font-size: 15px;font-weight: 600;margin-left: 79px;">USER: {{ user.username }}</b>
              </el-col>
              <el-col :span="8">
                <b style="color: white; font-size: 15px;font-weight: 600;margin-left: 80px;">SHIFT:&nbsp;&nbsp;&nbsp;{{ shift }}</b>
              </el-col>
            </el-row>
          </el-card>
        </el-col>
        <el-col :span="8">
          <b style="font-size: 57px;color: #fe0002;float: right;">{{ dateTime }}</b>
        </el-col>
      </el-row>
    </transition>
    <hr><br>
    <transition name="fade">
      <el-row :gutter="10">
