          <a href="#/transactions">
            <el-card shadow="hover" id="tile" class="tile-purple">
              <el-row>
                <el-col>
                  <div class="tile-icon">
                    <i class="fa fa-cc-mastercard"></i>
                  </div>
                </el-col>
              </el-row>
              <h4><b>Daily Transactions</b></h4>
            </el-card>
          </a>
        </el-col>
        <el-col :span="8">
          <a href="#/promo_items">
            <el-card shadow="hover" id="tile" class="tile-light-green">
              <el-row>
                <el-col>
                  <div class="tile-icon">
                    <i class="fa fa-list-alt"></i>
                  </div>
                </el-col>
              </el-row>
              <h4><b>Promo Items</b></h4>
            </el-card>
          </a>
        </el-col>
      </el-row>
    </transition>
    <div>
      <transition name="fade">
        <el-row :gutter="10">
          <el-col :span="8">
            <a href="#/points_redeem">
              <el-card shadow="hover" id="tile" class="tile-indigo">
                <el-row>
