            <a v-on:click='onSubmit' style="cursor: pointer">
              <el-card shadow="hover" id="tile" class="tile-deep-orange">
                <el-row>
                  <el-col>
                    <div class="tile-icon">
                      <i class="fa fa-eye"></i>
                    </div>
                  </el-col>
                </el-row>
                <h4><b>Total Points/Product Today</b></h4>
              </el-card>
            </a>
          </el-col>
          <el-col :span="8">
            <a v-on:click='exportDatabase' style="cursor: pointer">
              <el-card shadow="hover" id="tile" class="tile-amber">
                <el-row>
                  <el-col>
                    <div class="tile-icon">
                      <i class="fa fa-download" v-show="success"></i><i v-show="loading" class="fa fa-spinner"></i>
                    </div>
                  </el-col>
                </el-row>
                <h4><b>Export Database</b></h4>
              </el-card>
            </a>
          </el-col>
        </el-row>
      </transition>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import PointsModal from '@/components/PointsModal'
import { mapGetters } from 'vuex'
export default {
  name: 'HelloWorld',
  data () {
    return {
      selectShift: '',
      databases: '',
      dateTime: '',
      timeNow: '',
      selectedBranch: '',
      connectiondatabase: '',
      branch: '',
      connections: [],
      adminUser: false,
      success: true,
      loading: false,
      date: Date.now(),
      redeemDialog: false,
      page: 1,
      selectedProduct: ''
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('productManagement', {
      dailyPoints: 'dailyPoints'
    }),
    ...mapGetters('auth', {
      user: 'currentUser'
    }),
    ...mapGetters('auth', {
      shift: 'shift'
    }),
    ...mapGetters('databaseManagement', {
      exportedDatabases: 'exportDatabase',
      response: 'response'
    })
  },
  created  () {
    this.getDatabases()
    this.getDailyPoints()
    this.userPermission()
    this.getConnectionsFromLocalStorage()
    this.updateTimeSeconds()
    setInterval(this.updateTimeSeconds, 1000)
  },
  filters: {
    moment: function (date) {
      return moment(date).format('MMMM Do YYYY')
    }
  },
  methods: {
    selectShiftEvent () {
      if (this.selectShift !== null) {
        this.$store.dispatch('productManagement/getDailyPoints', {shift: this.selectShift})
      } else {
        this.$store.dispatch('productManagement/getDailyPoints', {shift: 0})
      }
    },
    updateTimeSeconds: function () {
      const date = Date.now()
      this.dateTime = moment(date).format('h:mm:ss A')
    },
    getDatabases () {
      this.$store.dispatch('databaseManagement/getDatabases')
    },
    getConnectionsFromLocalStorage () {
      const connections = localStorage.getItem('connections')
      this.connections = connections ? JSON.parse(connections) : []
      this.connectiondatabase = localStorage.getItem('connectiondatabase')
      this.branch = this.connections.filter(el => el.isSelected === true)
      this.selectedBranch = this.branch[0].branch_name
    },
    getDailyPoints () {
      this.$store.dispatch('productManagement/getDailyPoints', {shift: 0})
    },
    exportDatabase () {
      this.success = false
      this.loading = true
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('auth/getDatabase')
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.submitDatabase()
          this.loading = false
          this.success = true
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
    },
    submitDatabase () {
      const form = {
        user: this.user.username,
        path: process.env.DOMAIN_ENDPOINT + '/storage/' + JSON.parse(this.connectiondatabase) + '.sql',
        shift: this.shift
      }
      this.databases = this.exportedDatabases.filter(el => el.path === form.path)
      if (this.databases.length <= 0) {
        this.$store.dispatch('databaseManagement/addDatabase', form)
          .then(() => {
            this.$notify({
              title: 'Success',
              message: 'Successfully Exported Database Saved!',
              type: 'success',
              duration: 2000
            })
          })
      } else {
        this.$notify({
          title: 'Success',
          message: 'Successfully Exported Database Saved!',
          type: 'success',
          duration: 2000
        })
      }
    },
    goTo (url) {
      alert(url)
    },
    onCancel () {
      this.redeemDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    onSubmit () {
      this.redeemDialog = true
    },
    userPermission () {
      const userRole = this.user.roles.data[0].name
      if (userRole === 'sic') {
        this.sic = true
        this.fourCourt = true
        this.adminUser = false
      } else if (userRole === 'admin') {
        this.adminUser = true
        this.fourCourt = true
        this.sic = true
      } else {
        this.fourCourt = true
        this.sic = false
        this.adminUser = false
      }
    }
  },
  components: {
    'app-modal': PointsModal
  }
}
</script>

