<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <app-modal
      :title="title"
      :dialogVisible="viewDialog"
      :loading="isLoading"
      :disabled="isDisable"
      :showButtonSave="true"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Plate No./Barcode: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.barcode}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Last Name: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.last_name}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>First Name: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.first_name}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Middle Initial: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.middle_initial}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Birthdate: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.birthdate}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Contact No.: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.mobile_no}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Address: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.address}}</p></el-col>
        </el-row>
        <el-row :gutter="25" :data="customers">
          <el-col :span="6"><p class="text-right"><b>Customer Group: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.customer_group ? customerInfo.customer_group.name : ''}}</p>
          </el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Points: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.points}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Vehicles </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.vehicles}}</p></el-col>
        </el-row>
        <el-row :gutter="25">
          <el-col :span="6"><p class="text-right"><b>Expire At: </b></p></el-col>
          <el-col :span="18"><p>{{customerInfo.expire_at}}</p></el-col>
        </el-row>
      </div>
    </app-modal>
    <div>
        <el-row :gutter="24">
          <el-col :span="18">
            <el-autocomplete
              style="width: 95%;margin-left: 12px;"
              placeholder="Search Customer Here...."
              hide-loading
              v-model="selectedCustomer"
              :fetch-suggestions="remoteMethod"
              @select="selectCustomerEvent"
            ></el-autocomplete>
          </el-col>
          <el-col :span="6">
            <el-button @click="exportExcel" class="submit" style="width: 100%" icon="fa fa-download">
            EXPORT TO EXCEL &nbsp;
            </el-button>
          </el-col>
        </el-row>
      </div><br><hr><br>
    <el-table
      :data="customers"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props" id="expand">
          <p><b>Address:</b> {{ props.row.address }}</p>
          <p><b>Contact No.:</b> {{ props.row.mobile_no }}</p>
          <p><b>Current Points:</b> {{ props.row.points }}</p>
          <p><b>Customer Group:</b> {{ props.row.customer_group.name }}</p>
          <p><b>Vehicles:</b> {{ props.row.vehicles }}</p>
          <p><b>Expired at:</b> {{ props.row.expire_at }}</p>
        </template>
      </el-table-column>
      <el-table-column
        label="Plate No. / Barcode"
        prop="barcode">
      </el-table-column>
      <el-table-column
        label="Last Name"
        prop="last_name">
      </el-table-column>
      <el-table-column
        label="First Name"
        prop="first_name">
      </el-table-column>
      <el-table-column
        label="M.I"
        prop="middle_initial">
      </el-table-column>
      <el-table-column
        label="Address"
        prop="address">
      </el-table-column>
      <el-table-column
        label="Action">
        <template slot-scope="scope">
          <el-button
            icon="fa fa-eye"
            title="View Customer Information"
            circle
            @click="viewCustomer(scope.row)">
          </el-button>
          <el-button
            icon="fa fa-pencil"
            title="Edit Customer Information"
            type="success"
            circle
            @click="handleEdit(scope.row)">
          </el-button>
          <el-button
            icon="fa fa-trash"
            title="Delete Customer Information"
            class="colorPrimary"
            circle
            @click="deleteCustomer(scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
    </app-pagination>
  </div>
</template>

<script>

import Pagination from '@/components/Pagination'
import Modal from '@/components/Modal'
import { mapGetters } from 'vuex'
import _ from 'lodash'
export default {
  name: 'CustomersList',
  data () {
    return {
      selectedBranch: '',
      exportValue: 1,
      selectedCustomer: '',
      loading: false,
      buttonSaveVisible: false,
      title: '',
      deleteDialog: false,
      viewDialog: false,
      customerInfo: {},
      page: 1,
      queryValue: '',
      type: 'list'
    }
  },
  computed: {
    ...mapGetters('customer', {
      customers: 'customers',
      pagination: 'pagination'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('vehicleManagement', {
      vehicles: 'vehicles'
    })
  },
  watch: {
    page () {
      this.getCustomers()
      this.getCustomerGroup()
    }
  },
  created () {
    this.getCustomers()
    this.getCustomerGroup()
  },
  methods: {
    getConnectionsFromLocalStorage () {
      const connections = localStorage.getItem('connections')
      this.connections = connections ? JSON.parse(connections) : []
      this.connectiondatabase = localStorage.getItem('connectiondatabase')
      this.branch = this.connections.filter(el => el.isSelected === true)
      this.selectedBranch = this.branch[0].branch_name
    },
    exportExcel () {
      const params = {
        s: this.queryValue ? this.queryValue : '',
        s_field: 'first_name,last_name',
        export: this.exportValue,
        type: this.type,
        station: this.selectedBranch
      }
      if (params !== '') {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/customer?s=' + params.s + '&s_field=' + params.s_field + '&export=' + params.export + '&type=' + params.type + '&station=' + params.station
      }
      window.location.href = this.url
    },
    remoteMethod: _.debounce(function (query) {
      this.loading = true
      this.$store.dispatch('customer/getCustomers', {
        s: query,
        s_field: 'first_name,last_name,barcode',
        page: this.page,
        export: 0,
        type: this.type,
        station: this.station
      }).then(() => {
        this.queryValue = query
        this.loading = false
      })
    }, 1000),
    selectCustomerEvent () {
      if (this.selectedCustomer === '') {
        this.$store.dispatch('customer/getCustomers', {
          s: '',
          s_field: 'first_name,last_name',
          page: this.page
        }).then(() => {
          this.loading = false
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
        })
      }
    },
    getVehicles () {
      this.$store.dispatch('vehicleManagement/getVehicles', {page: null})
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', {page: null})
    },
    async getCustomers () {
      const page = this.page
      this.$store.dispatch('customer/getCustomers', { page })
    },
    viewCustomer (item) {
      this.viewDialog = true
      this.buttonSaveVisible = true
      this.title = 'View Customer Info'
      this.customerInfo = Object.assign({}, item)
    },
    handleEdit (item) {
      this.customerInfo = Object.assign({}, item)
      this.$store.commit('customer/setSelectedCustomer', this.customerInfo)
      this.$router.push('/customers/update')
    },
    deleteCustomer (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.customerInfo = Object.assign({}, item)
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('customer/deleteCustomer', this.customerInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Record Deleted Successfully!',
            type: 'success',
            duration: 2000
          })
          this.getCustomers()
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.deleteDialog = false
      this.viewDialog = false
      this.buttonSaveVisible = false
    },
    handleClose (done) {
      this.onCancel()
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination,
    'app-modal': Modal
  }
}
</script>

