<template>
    <div>
        <center>
            <br><br>
            <el-row :gutter="20">
                <el-col :span="20" :offset="2">
                    <h1>Tap your Card Now</h1>
                    <!-- <el-input name="code" @input="onInputBarcode" v-model="barcode" autofocus class="input code" placeholder="Please Tap Your Card To Inquire..."></el-input> -->
                </el-col>
            </el-row>
        </center>
        <div>
            <br><br><br>
            <fieldset v-show="showCustomer">
            <legend><strong>Customer Details</strong></legend>
            <div style="margin-left: 60%;margin-top: 28px;"><p v-show="displayResult.first_name">REMAINING POINTS: <br></p>&nbsp; <b class="points-available">{{displayResult.points}}</b></div>
                <el-col><br>
                    <el-table
                    :data="filteredCustomer"
                    border
                    style="width: 100%">
                    <el-table-column
                    label="CUSTOMER NAME">
                    <template slot-scope="scope">
                    {{ scope.row.first_name + ' ' + scope.row.last_name}}
                    </template>
                    </el-table-column>
                    <el-table-column
                    prop="address"
                    label="ADDRESS">
                    </el-table-column>
                    <el-table-column
                    prop="customer_group_id"
                    label="CUSTOMER GROUP">
                    <template slot-scope="scope">
                        {{ scope.row.customer_group.name}}
                    </template>
                    </el-table-column>
                    <el-table-column
                    prop="vehicles"
                    label="VEHICLE">
                    </el-table-column>
                    <el-table-column
                    prop="mobile_no"
                    label="MOBILE NUMBER">
                    </el-table-column>
                    </el-table>
                </el-col>
            </fieldset>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'
import _ from 'lodash'
export default {
  data () {
    return {
      barcode: '',
      isFiltered: false,
      showCustomer: false
    }
  },
  created () {
    this.$barcodeScanner.init(this.onBarcodeScanned)
  },
  computed: {
    ...mapGetters('transaction', {
      filteredCustomer: 'transactionByBarcode'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    displayResult () {
      if (this.filteredCustomer.length === 1) {
        return this.filteredCustomer[0]
      }
      return ''
    }
  },
  methods: {
    onInputBarcode: _.debounce(function (event) {
      // this.barcode = event
      if (this.barcode !== '') {
        this.isFiltered = true
        this.loading = true
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.dispatch('transaction/getCustomerByBarcode', {barcode: 'mg-' + this.barcode})
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            if (this.filteredCustomer.length === 1) {
              this.loading = false
              this.isSuccess = false
              this.showCustomer = true
            } else {
              this.isSuccess = true
              this.$notify({
                title: 'Error',
                message: 'Barcode doesnt exists!',
                type: 'error',
                duration: 3000
              })
            }
          })
          .catch(error => {
            this.$notify({
              title: 'Error',
              message: 'Somethig went wrong!',
              type: 'error',
              duration: 1000
            })
            throw new Error(error)
          })
      } else {
        this.isFiltered = false
      }
    }, 500),
    clear_form: function () {
      this.barcode = ''
    },
    onBarcodeScanned (barcode) {
      this.barcode = barcode
    },
    resetBarcode () {
      let barcode = this.$barcodeScanner.getPreviousCode()
      console.log(barcode)
    }
  },
  watch: {
    barcode () {
      this.onInputBarcode()
    }
  }
}
</script>
