<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <el-button @click="exportPointSystemSummary" class="submit" style="width: 23%;float: right" icon="fa fa-download">
    EXPORT TO EXCEL &nbsp;
    </el-button>
    <br><br><br>
    <el-row :gutter="10">
      <el-col :span="12">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-plus"></i> New Point System</span>
          </div>
          <el-form :model="ruleForm" label-position="left" label-width="250px">
            <el-form-item label="CUSTOMER GROUP">
              <el-select v-model="ruleForm.customer_group_id" placeholder="CUSTOMER GROUP" name="Customer Group" v-validate="'required'">
                <el-option
                  v-for="item in customerGroups"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
              <span class="text-error">{{ errors.first('Customer Group') }}</span>
            </el-form-item>
            <el-form-item label="SELECT PRODUCT">
              <el-select v-model="ruleForm.product_id" placeholder="SELECT PRODUCT" name="Product" v-validate="'required'">
                <el-option
                  v-for="item in prices"
                  :key="item.id"
                  :label="item.product.product_name"
                  :value="item.product.id"
                  >
                </el-option>
              </el-select>
              <span class="text-error">{{ errors.first('Product') }}</span>
            </el-form-item>
            <el-form-item label="EQUIVALENT POINTS (per LITER)">
              <el-input placeholder="Points" v-model="ruleForm.equivalent_points" type="number" name="Points" v-validate="{required: true, decimal: 5}"></el-input>
            <span class="text-error">{{ errors.first('Points') }}</span>
            </el-form-item>
            <el-form-item>
              <el-button style="float: right" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="12" style="padding-right: 2px">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-list"></i> List of Points</span>
          </div>
          <el-table
            :data="points"
            border
            style="width: 100%">
            <el-table-column
              prop="customer_group_id"
              label="CUSTOMER GROUP"
              width="187">
            <template slot-scope="scope">
              {{scope.row.customer_group.name}}
            </template>
            </el-table-column>
            <el-table-column
              prop="product_id"
              label="PRODUCT"
              width="110">
            <template slot-scope="scope">
              {{scope.row.product.product_name}}
            </template>
            </el-table-column>
            <el-table-column
              prop="equivalent_points"
              label="POINTS">
            </el-table-column>
            <el-table-column
              label="Action">
              <template slot-scope="scope">
                <el-button
                  icon="fa fa-pencil"
                  title="Edit Customer Information"
                  type="success"
                  circle
                  @click="handleEdit(scope.row)">
                </el-button>
                <el-button
                  v-show="pointDisplay"
                  icon="fa fa-trash"
                  title="Delete Customer Information"
                  class="colorPrimary"
                  circle
                  @click="handleDelete(scope.row)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
         </app-pagination>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>

import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import Modal from '@/components/Modal'

export default {
  data () {
    return {
      pointDisplay: false,
      isEdit: false,
      ruleForm: {
        customer_group_id: '',
        product_id: '',
        point: ''
      },
      title: '',
      deleteDialog: false,
      pointsInfo: [],
      page: 1
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('productManagement', {
      products: 'products'
    }),
    ...mapGetters('priceManagement', {
      prices: 'prices',
      pagination: 'pagination',
      response: 'response'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups',
      response: 'response'
    }),
    ...mapGetters('pointsSystem', {
      points: 'points',
      pagination: 'pagination',
      response: 'response'
    }),
    ...mapGetters('auth', {
      shift: 'shift',
      user: 'currentUser'
    })
  },
  watch: {
    page () {
      this.getCustomerGroup()
      this.getProducts()
      this.getPoints()
      this.getPrices()
    }
  },
  created () {
    this.getProducts()
    this.getCustomerGroup()
    this.getPoints()
    this.getPrices()
  },
  methods: {
    exportPointSystemSummary () {
      this.url = process.env.VUE_APP_API_ENDPOINT + '/export-point-system-summary'
      window.location.href = this.url
    },
    getProducts () {
      this.$store.dispatch('productManagement/getProducts', {page: null})
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', { page: null })
    },
    getPoints () {
      const page = this.page
      this.$store.dispatch('pointsSystem/getPoints', { page })
    },
    getPrices () {
      this.$store.dispatch('priceManagement/getPrices', { page: null })
    },
    clear_form: function () {
      this.ruleForm = {
        customer_group_id: '',
        product_id: '',
        point: ''
      }
      this.$nextTick(() => {
        this.$validator.reset()
      })
    },
    submitForm () {
      const form = {
        id: this.ruleForm.id,
        customer_group_id: this.ruleForm.customer_group_id,
        product_id: this.ruleForm.product_id,
        equivalent_points: this.ruleForm.equivalent_points,
        shift: this.shift,
        username: this.user.username
      }
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updatePoint(form)
          } else {
            this.addPoint(form)
          }
        }
      })
    },
    handleEdit (item) {
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
    },
    handleDelete (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.pointsInfo = Object.assign({}, item)
    },
    async addPoint (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('pointsSystem/addPoint', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.points.unshift(this.response)
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Point System Already Exist!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async updatePoint (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('pointsSystem/updatePoint', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Successfully Updated!',
            type: 'success',
            duration: 2000
          })
          this.getPoints()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('pointsSystem/deletePoint', this.pointsInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'SuccessFully Deleted!',
            type: 'success',
            duration: 2000
          })
          const index = this.points.findIndex(value => value.id === this.pointsInfo.id)
          this.points.splice(index, 1)
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.deleteDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    handlePagination (page) {
      this.page = page
    },
    validateForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.$notify({
            title: 'Success',
            message: 'Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.$nextTick(() => {
            this.$validator.reset()
          })
          this.clear_form()
        }
      })
    }
  },
  components: {
    'app-pagination': Pagination,
    'app-modal': Modal
  }
}
</script>

<style scoped>
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.clearfix {
  font-size: 18px;
}
.el-select {
  width: 100%;
}
.el-date-editor.el-input {
  width: 100%;
}
</style>
