export default {
  getCustomers (payload = {}) {
    const params = {
      s: payload.s,
      s_field: payload.s_field,
      page: payload.page,
      export: payload.export,
      type: payload.type,
      customer_group: payload.customer_group
    }
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/customer', { params })
  },
  addCustomer (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/customer', payload)
  },
  updateCustomer (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/customer/${payload.id}`, payload)
  },
  deleteCustomer (payload = {}) {
    return axios.delete(process.env.VUE_APP_API_ENDPOINT + `/customer/${payload.id}`, payload)
  }
}
