import api from '../api/productManagement'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    products: [],
    pagination: '',
    response: [],
    dailyPoints: []
  },
  getters: {
    products: state => state.products,
    productsWithPoints (state) {
      const items = []
      state.products.forEach(item => {
        if (item.point_system) {
          items.push({...item})
        }
      })
      return items
    },
    pagination: state => state.pagination,
    response: state => state.response,
    dailyPoints: state => state.dailyPoints
  },
  mutations: {
    [types.mutations.productManagement.setProducts] (state, payload) {
      if (payload.data === undefined) {
        state.products = payload
        state.pagination = payload
      } else {
        state.products = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.productManagement.setDailyPoints] (state, payload) {
      state.dailyPoints = payload
    },
    [types.mutations.productManagement.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.productManagement.getDailyPoints] ({ commit }, payload) {
      try {
        const response = await api.getDailyPoints(payload)
        commit(types.mutations.productManagement.setDailyPoints, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.productManagement.getProducts] ({ commit }, payload) {
      try {
        const response = await api.getProducts(payload)
        commit(types.mutations.productManagement.setProducts, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.productManagement.addProduct] ({ commit }, payload) {
      try {
        const response = await api.addProduct(payload)
        commit(types.mutations.productManagement.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.productManagement.updateProduct] (state, payload) {
      try {
        await api.updateProduct(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.productManagement.deleteProduct] (state, payload) {
      try {
        await api.deleteProduct(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
