import types from '../../types'
import api from '../api/dbConnection'
export default {
  namespaced: true,
  state: {

  },
  getters: {

  },
  mutations: {

  },
  actions: {
    async [types.actions.dbCon.testConnection] (state, payload) {
      try {
        await api.testConnection(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.dbCon.selectConnection] (state, payload) {
      try {
        await api.selectConnection(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
