export default {
  getRoles () {
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/role')
  },
  getUsers () {
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/users?include=roles')
  },
  addUser (payload) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/users', payload)
  },
  updateUser (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/users/${payload.id}`, payload)
  },
  updateUserPassword (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/password-update/${payload.id}`, payload)
  },
  deleteUser (payload = {}) {
    return axios.delete(process.env.VUE_APP_API_ENDPOINT + `/users/${payload.id}`, payload)
  },
  create () {},
  update () {},
  delete () {}
}
