import api from '../api/priceManagement'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    prices: [],
    pagination: '',
    response: []
  },
  getters: {
    prices: state => state.prices,
    pagination: state => state.pagination,
    response: state => state.response
  },
  mutations: {
    [types.mutations.priceManagement.setPrices] (state, payload) {
      if (payload.data === undefined) {
        state.prices = payload
        state.pagination = payload
      } else {
        state.prices = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.priceManagement.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.priceManagement.getPrices] ({ commit }, payload) {
      try {
        const response = await api.getPrices(payload)
        commit(types.mutations.priceManagement.setPrices, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.priceManagement.addPrice] ({ commit }, payload) {
      try {
        const response = await api.addPrice(payload)
        commit(types.mutations.priceManagement.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.priceManagement.updatePrice] (state, payload) {
      try {
        await api.updatePrice(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.priceManagement.deletePrice] (state, payload) {
      try {
        await api.deletePrice(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
