import api from '../api/transaction'
import types from '../../types'

export default {
  namespaced: true,
  state: {
    transactions: [],
    transactionByBarcode: [],
    transaction: [],
    customerTransaction: [],
    pagination: '',
    transactionCustomer: []
  },
  getters: {
    transactions: state => state.transactions,
    performTransactions: state => state.transactions,
    transactionByBarcode: state => state.transactionByBarcode,
    pagination: state => state.pagination,
    customerTransaction: state => state.customerTransaction,
    transactionCustomer: state => state.transactionCustomer
  },
  mutations: {
    [types.mutations.transaction.setTransactions] (state, payload) {
      if (payload.data === undefined) {
        state.transactions = payload
        state.pagination = payload
      } else {
        state.transactions = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.transaction.setSelectedCustomerByTransaction] (state, payload) {
      state.transaction = payload
    },
    [types.mutations.transaction.setCustomerByBarcode] (state, payload) {
      state.transactionByBarcode = payload
    },
    [types.mutations.transaction.setSelectedTransaction] (state, payload) {
      state.customerTransaction = payload
    },
    [types.mutations.transaction.setTransactionByCustomer] (state, payload) {
      state.transactionCustomer = payload
    }
  },
  actions: {
    async [types.actions.transaction.getTransactions] ({commit}, payload) {
      try {
        const response = await api.getTransactions(payload)
        commit(types.mutations.transaction.setTransactions, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.transaction.addTransaction] (state, payload) {
      try {
        await api.addTransaction(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.transaction.getCustomerByBarcode] ({ commit }, payload) {
      try {
        const response = await api.getCustomerByBarcode(payload)
        commit(types.mutations.transaction.setCustomerByBarcode, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.transaction.getTransactionByCustomer] ({ commit }, payload) {
      try {
        const response = await api.getTransactionByCustomer(payload)
        commit(types.mutations.transaction.setTransactionByCustomer, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.transaction.getSelectedTransaction] ({ commit }, payload) {
      try {
        const response = await api.getSelectedTransaction(payload)
        commit(types.mutations.transaction.setSelectedTransaction, response.data)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
