import api from '../api/user'
import types from '../../types'

export default {
  namespaced: true,
  state: {
    users: [],
    roles: [],
    response: []
  },
  getters: {
    users: state => state.users,
    roles: state => state.roles,
    response: state => state.response
  },
  mutations: {
    [types.mutations.users.setUsers] (state, payload) {
      if (payload.data === undefined) {
        state.users = payload
      } else {
        state.users = payload.data
      }
    },
    [types.mutations.users.setRoles] (state, payload) {
      state.roles = payload.data
    },
    [types.mutations.users.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.users.getUsers] ({ commit }, payload) {
      try {
        const response = await api.getUsers(payload)
        commit(types.mutations.users.setUsers, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.users.getRoles] ({ commit }, payload) {
      try {
        const response = await api.getRoles(payload)
        commit(types.mutations.users.setRoles, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.users.addUser] ({ commit }, payload) {
      try {
        const response = await api.addUser(payload)
        commit(types.mutations.users.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.users.updateUser] (state, payload) {
      try {
        await api.updateUser(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.users.updateUserPassword] ({ commit }, payload) {
      try {
        const response = await api.updateUserPassword(payload)
        commit(types.mutations.users.setUsers, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.users.deleteUser] (state, payload) {
      try {
        await api.deleteUser(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
