export default {
  getPoints (payload = {}) {
    const page = payload.page !== null ? `?page=${payload.page}` : ''
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/point-system' + page)
  },
  addPoint (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/point-system', payload)
  },
  updatePoint (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/point-system/${payload.id}`, payload)
  },
  deletePoint (payload = {}) {
    return axios.delete(process.env.VUE_APP_API_ENDPOINT + `/point-system/${payload.id}`, payload)
  }
}
