import api from '../api/pointsRedeem'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    redeemPoints: [],
    pagination: '',
    response: []
  },
  getters: {
    redeemPoints: state => state.redeemPoints,
    pagination: state => state.pagination,
    response: state => state.response
  },
  mutations: {
    [types.mutations.pointsRedeem.setPointsRedeem] (state, payload) {
      if (payload.data === undefined) {
        state.redeemPoints = payload
        state.pagination = payload
      } else {
        state.redeemPoints = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.pointsRedeem.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.pointsRedeem.getPointsRedeem] ({ commit }, payload) {
      try {
        const response = await api.getPointsRedeem(payload)
        commit(types.mutations.pointsRedeem.setPointsRedeem, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.pointsRedeem.addPointsRedeem] (state, payload) {
      try {
        await api.addPointsRedeem(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
