<template>
<el-container style="overflow:hidden">
  <el-container class="el-container">
<div class="content">
<el-card class="box-card">
  <div slot="header" class="clearfix">
    <el-row>
      <el-col :span="6"><img src="@/assets/logo.jpg" class="logo"></el-col>
      <el-col :span="15"><br>
        <h2>Log In</h2>
      </el-col>
      <el-col :span="3">
       <el-button plain icon="el-icon-setting" type="info" class="setting" @click="dialogVisible = true"></el-button>
      </el-col>
    </el-row>
  </div>
 <el-form ref="form" label-width="20px" class="form" @keyup.native.enter="login">
   <el-form-item>
     <label>Select Shift</label>
      <el-select name="Shift" :class="errors.has('Shift')? 'is-error':''" v-validate="'required'" placeholder="Select Shift" v-model="form.shift" class="sel">
        <el-option label="Shift 1" value="1"></el-option>
        <el-option label="Shift 2" value="2"></el-option>
        <el-option label="Shift 3" value="3"></el-option>
      </el-select>
      <span v-show="errors.has('Shift')" class="text-error">{{ errors.first('Shift') }}</span>
  </el-form-item>
  <el-form-item><label>Username</label>&nbsp;
    <el-input v-model="form.username" prefix-icon="el-icon-user" placeholder="Please input username" clearable class="sel"></el-input>
  </el-form-item>
  <el-form-item><label>Password</label>&nbsp;
    <el-input v-model="form.password" type="password" prefix-icon="el-icon-lock" placeholder="Please input password" clearable class="sel"></el-input>
  </el-form-item>
  <el-form-item style="margin-left:15%">
    <el-button type='submit' class="submit" icon="el-icon-check" :disabled="isNull_log || isDisable" :loading="isLoading" round @click="login">Submit</el-button>
    <el-button type="danger" plain round>Cancel</el-button>
  </el-form-item>
</el-form>
</el-card>
</div>
  </el-container>
  <el-dialog
        :visible.sync="dialogVisible"
       >
        <el-row>
          <el-col :span="3">
            <img src="@/assets/conn.png" style="width:50%;height:50%;margin-top:-50%">
          </el-col>
          <el-col :span="10">
            <h2 style="margin-top:-8%">Choose Connection</h2>
          </el-col>
        </el-row>
        <el-row :gutter="5">
          <el-col :span="12">
              <el-table
            :data="connections"
            height="350"
            style="width:100%">
            <el-table-column width="350">
            <template slot-scope="scope">
            <el-button type="text" size="mini" @click="deleteConnectionsFromLocalStorage(scope.row)">
              <i class="fa fa-times-circle" style="color: #606266"></i>
            </el-button>
            <el-button type="text" size="mini" style="color: #606266;font-size: 13px" @click="selectConnection(scope.row)">
              <i class="fa fa-plug"></i>
              <span>{{ scope.row.branch_name }} ({{ scope.row.db_name }}) <i class="fa fa-check" style="color: #5daf34" v-show="scope.row.isSelected"></i></span>
            </el-button>
            </template>
            </el-table-column>
          </el-table>
          </el-col>
           <el-col :span="12">
            <el-input placeholder="Branch Name" clearable style="margin-bottom:10px;" v-model="branch_name"></el-input>
            <el-input placeholder="Database Name" clearable style="margin-bottom:10px;" v-model="db_name"></el-input>
            <el-button type="submit" class="submit" :disabled="isNull || isDisable" @click="testConnect" :loading="isLoading">Test Connection</el-button>
            <el-button type="info" class="submit" :disabled="isNull || isSuccess" @click="save">Save</el-button>
          </el-col>
        </el-row>
      </el-dialog>
</el-container>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Login',
  data () {
    return {
      selectArray: '',
      check: false,
      form: {
        username: '',
        password: '',
        form: true,
        shift: ''
      },
      connect: '',
      connections: [],
      connectiondatabase: [],
      db_name: '',
      branch_name: '',
      dialogVisible: false,
      isSuccess: true,
      message: '',
      type: ''
    }
  },
  created () {
    // this will get the connections save from localStorage if exist
    this.getConnectionsFromLocalStorage()
  },
  methods: {
    login () {
      if (this.form.shift !== '') {
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.commit('appUI/setIsDisable', true)
        this.$store.dispatch('auth/authLogin', {
          username: this.form.username,
          password: this.form.password,
          grant_type: process.env.VUE_APP_GRANT_TYPE,
          client_id: process.env.VUE_APP_CLIENT_ID,
          client_secret: process.env.VUE_APP_CLIENT_SECRET
        })
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            this.$store.commit('appUI/setIsDisable', false)
            this.$store.commit('auth/setShift', this.form.shift)
            this.turn = false
            this.$notify({
              title: 'Success',
              message: 'Logged-in Successfully!',
              type: 'success',
              duration: 2000
            })
            this.$store.commit('auth/setIsLogin', true)
          })
          .catch(error => {
            this.$store.commit('appUI/setIsLoading', false)
            this.$store.commit('appUI/setIsDisable', false)
            this.$notify.error({
              title: 'Error',
              message: 'Invalid credentials!',
              duration: 2000
            })
            throw new Error(error)
          })
      } else {
        this.$notify.error({
          title: 'Error',
          message: 'Shift field is required!',
          duration: 2000
        })
      }
    },
    testConnect () {
      // set loading and disable to true
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('dbConnection/testConnection', {
        db_name: this.db_name,
        action: 'test'
      })
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.message = 'Connection Success'
          this.type = 'success'
          this.isSuccess = false
          this.successDialog()
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.isSuccess = true
          this.$notify.error({
            title: 'Error',
            message: 'Connection Failed',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    successDialog () {
      this.$notify({
        title: 'Success',
        message: this.message,
        type: this.type,
        duration: 2000
      })
    },
    save () {
      const connection = {
        branch_name: this.branch_name,
        db_name: this.db_name,
        isSelected: false
      }
      const id = this.connections.findIndex(el => el.db_name === this.db_name)
      if (id < 0) {
        this.connections.push(connection)
        // add connection in local storage
        localStorage.setItem('connections', JSON.stringify(this.connections))
        this.message = 'Database Saved'
        this.type = 'success'
        this.successDialog()
      } else {
        this.$notify.error({
          title: 'Error',
          message: 'Database is Already Use',
          duration: 2000
        })
      }
    },
    getConnectionsFromLocalStorage () {
      const connections = localStorage.getItem('connections')
      this.connections = connections ? JSON.parse(connections) : []
    },
    deleteConnectionsFromLocalStorage (row) {
      const dbName = row.db_name
      const sample = this.connections.findIndex(value => value.db_name === dbName)
      this.connections.splice(sample, 1)
      localStorage.setItem('connections', JSON.stringify(this.connections))
      localStorage.removeItem('connectiondatabase', JSON.stringify(this.connectiondatabase))
    },
    selectConnection (data) {
      this.$store.dispatch('dbConnection/selectConnection', {
        db_database: data.db_name
      })
      const connectiondatabase = data.db_name
      localStorage.setItem('connectiondatabase', JSON.stringify(connectiondatabase))
      this.connect = this.connections.find(value => value.db_name === connectiondatabase)
      this.connect['isSelected'] = true
      this.connections.forEach(element => {
        (element.db_name === this.connect.db_name) ? element.isSelected = true : element.isSelected = false
      })
      // add connection in local storage
      localStorage.setItem('connections', JSON.stringify(this.connections))
      this.message = 'Database' + ' ' + connectiondatabase + ' ' + 'is set'
      this.type = 'success'
      this.successDialog()
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('auth', {
      isLogin: 'isLogin'
      // user_details: 'user_details'
    }),
    isNull: function () {
      if (this.db_name === '' || this.branch_name === '') {
        return true
      } else {
        return false
      }
    },
    isNull_log: function () {
      if (this.form.shift === '' || this.form.username === '' || this.form.password === '') {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
