import api from '../api/databaseManagement'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    exportDatabase: [],
    response: []
  },
  getters: {
    exportDatabase: state => state.exportDatabase,
    response: state => state.response
  },
  mutations: {
    [types.mutations.databaseManagement.setDatabases] (state, payload) {
      if (payload.data === undefined) {
        state.exportDatabase = payload
      } else {
        state.exportDatabase = payload.data
      }
    },
    [types.mutations.databaseManagement.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.databaseManagement.getDatabases] ({ commit }, payload) {
      try {
        const response = await api.getDatabases(payload)
        commit(types.mutations.databaseManagement.setDatabases, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.databaseManagement.addDatabase] ({ commit }, payload) {
      try {
        const response = await api.addDatabase(payload)
        commit(types.mutations.databaseManagement.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}
