<template>
  <div>
    <el-form :inline="true" :model="formInline" class="demo-form-inline" label-width="130px">
      <el-row :gutter="10">
        <el-form-item label="Select Date">
          <el-date-picker
            @change="selectDateOnly"
            v-model="dateOnly"
            type="date"
            placeholder="Pick a date"
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="Select Shift">
          <el-select @change="selectShiftOnly" clearable v-model="selectShift" placeholder="----- SELECT -----">
            <el-option label="Shift 1" value="1"></el-option>
            <el-option label="Shift 2" value="2"></el-option>
            <el-option label="Shift 3" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item id="form-item" style="float:right">
          <el-button @click="exportExcel" class="submit" icon="fa fa-download">&nbsp;EXPORT TO EXCEL</el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <el-row>
      <el-col :span="24">
        <el-card shadow="never">
          <el-table
            :data="redeemPoints"
            style="width: 100%"
            height="400">
            <el-table-column
              label="Serial Number"
              prop="serial_no">
            </el-table-column>
            <el-table-column
              label="Customer Name"
              prop="customer_id">
            <template slot-scope="scope">
              {{ scope.row.customer.first_name}}
            </template>
            </el-table-column>
            <el-table-column
              label="Amount Redeem"
              prop="amount">
            </el-table-column>
            <el-table-column
              label="Convertion Date"
              prop="created_at">
            <template slot-scope="scope">
              {{ toDateString(scope.row.created_at) }}
            </template>
            </el-table-column>
            <el-table-column
              label="Points Converted"
              prop="amount">
            </el-table-column>
          </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
          </app-pagination>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-card shadow="never">
          <el-form :inline="true" style="float: right" class="demo-form-inline">
            <el-form-item id="label" label="TOTAL POINTS CONVERTED">
              <el-input readonly="" v-model="totalpoints"></el-input>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
export default {
  name: 'PointConversion',
  data () {
    return {
      url: '',
      exportValue: 1,
      fromDate: '',
      toDate: '',
      oneDate: '',
      customerOnly: '',
      shiftOnly: '',
      selectShift: '',
      dateOnly: '',
      page: 1,
      total_points_converted: '200',
      formInline: {
        date: Date.now(),
        shift: ''
      },
      tableData3: [{
        customer_name: 'Juan dela Cruz',
        details: 'Fill Update 2018-07-07',
        conversion_date: '2018-07-01',
        product_availed: 'CONVERT TO CASH PHP 200',
        points_converted: '200'
      }]
    }
  },
  computed: {
    ...mapGetters('auth', {
      shift: 'shift'
    }),
    ...mapGetters('pointsRedeem', {
      redeemPoints: 'redeemPoints',
      pagination: 'pagination'
    }),
    totalpoints: function () {
      if (this.redeemPoints.length > 0) {
        const amountArr = this.redeemPoints.map(el => el.amount)
        const reducer = (accumulator, currentValue) => accumulator + currentValue
        return amountArr.reduce(reducer)
      }
      return 0
    }
  },
  watch: {
    page () {
      this.getPointsRedeem()
    }
  },
  created () {
    this.getPointsRedeem()
  },
  methods: {
    exportExcel () {
      this.fromDate = 0
      this.toDate = 0
      this.oneDate = this.dateOnly ? this.dateOnly : 0
      this.customerOnly = 0
      this.shiftOnly = this.selectShift ? this.selectShift : 0
      if (this.oneDate !== 0) {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/points-redeem?filter_by_customer=' + this.customerOnly + '&filter_customer_date=' + this.oneDate + '&filter_by_shift=' + this.shiftOnly + '&export=' + this.exportValue
      } else {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/points-redeem?filter_by_customer=' + this.customerOnly + '&export=' + this.exportValue
      }
      window.location.href = this.url
    },
    getPointsRedeem () {
      const page = this.page
      this.$store.dispatch('pointsRedeem/getPointsRedeem', {
        customer_id: 0,
        page,
        from_date: 0,
        to_date: 0,
        dateOnly: 0,
        shift: 0,
        export: 0
      })
    },
    selectDateOnly () {
      if (this.dateOnly !== null) {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: 0,
          page,
          from_date: 0,
          to_date: 0,
          dateOnly: this.dateOnly,
          shift: this.selectShift ? this.selectShift : 0,
          export: 0
        })
      } else {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: 0,
          page,
          from_date: 0,
          to_date: 0,
          dateOnly: 0,
          shift: 0,
          export: 0
        })
      }
    },
    selectShiftOnly () {
      if (this.selectShift !== null) {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: 0,
          page,
          from_date: 0,
          to_date: 0,
          dateOnly: this.dateOnly ? this.dateOnly : 0,
          shift: this.selectShift,
          export: 0
        })
      } else {
        const page = this.page
        this.$store.dispatch('pointsRedeem/getPointsRedeem', {
          customer_id: 0,
          page,
          from_date: 0,
          to_date: 0,
          dateOnly: 0,
          shift: 0,
          export: 0
        })
      }
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination
  }
}
</script>

