export default {
  getPointsRedeem (payload = {}) {
    const page = payload.page !== null ? `&page=${payload.page}` : ''
    if (payload.customer_id === 0 && payload.from_date === 0 && payload.dateOnly === 0 && payload.shift === 0) {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/points-redeem?filter_by_customer=' + payload.customer_id + page + '&export=' + payload.export)
    } else if (payload.from_date !== 0 && payload.to_date !== 0) {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/points-redeem?filter_by_customer=' + payload.customer_id + page + '&from_date=' + payload.from_date + '&to_date=' + payload.to_date)
    } else {
      return axios.get(process.env.VUE_APP_API_ENDPOINT + '/points-redeem?filter_by_customer=' + payload.customer_id + page + '&filter_customer_date=' + payload.dateOnly + '&filter_by_shift=' + payload.shift + '&export=' + payload.export)
    }
  },
  addPointsRedeem (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/points-redeem', payload)
  }
}
