export default {
  getVehicles (payload = {}) {
    const page = payload.page !== null ? `?page=${payload.page}` : ''
    return axios.get(process.env.VUE_APP_API_ENDPOINT + '/vehicle' + page)
  },
  addVehicle (payload = {}) {
    return axios.post(process.env.VUE_APP_API_ENDPOINT + '/vehicle', payload)
  },
  updateVehicle (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.VUE_APP_API_ENDPOINT + `/vehicle/${payload.id}`, payload)
  },
  deleteVehicle (payload = {}) {
    return axios.delete(process.env.VUE_APP_API_ENDPOINT + `/vehicle/${payload.id}`, payload)
  }
}
