
import momentTimeZone from 'moment-timezone'

export default {
  methods: {
    toDateString (date) {
      return momentTimeZone.utc(date).tz('Asia/Manila').format('YYYY-MM-DD')
    },
    toDateTimeString (date) {
      return momentTimeZone.utc(date).tz('Asia/Manila').format('hh:mm A')
    }
  }
}
