<template>
    <div>
     <el-row :gutter="5">
        <el-col :span="5">
            <el-select name="group" clearable @change="selectedCustomerGroupEvent" v-validate="'required'" v-model="selectedCustomerGroup" placeholder="Customer Group">
              <el-option
                v-for="item in customerGroups"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-col>
        <el-col :span="15">
          <span style="width: auto;" class="demo-input-label">Search</span>
            <el-autocomplete
              hide-loading
              v-model="selectedCustomer"
              :fetch-suggestions="remoteMethod"
              placeholder="Please Input Customer"
              @select="selectCustomerEvent"
              style="width: 80%"
            ></el-autocomplete>
        </el-col>
        <el-col :span="4">
            <el-button @click="exportExcel" icon="fa fa-download" width="180" style="background-color:yellow">&nbsp;&nbsp;Export to <strong>Excel</strong></el-button>
        </el-col>
     </el-row><br>
    <el-table
    :data="customers"
    border>
    <el-table-column
      label="NAME"
      fixed
      width="180">
    <template slot-scope="scope">
      {{ scope.row.first_name + ' ' + scope.row.last_name}}
    </template>
    </el-table-column>
    <el-table-column
      prop="barcode"
      label="BARCODE"
      width="170">
    </el-table-column>
     <el-table-column
      prop="address"
      label="ADDRESS"
      width="170">
    </el-table-column>
     <el-table-column
      prop="customer_group_id"
      label="GROUP"
       width="290">
    <template slot-scope="scope">
      {{ scope.row.customer_group.name}}
    </template>
     </el-table-column>
    <el-table-column
      prop="points"
      label="CURRENT POINTS"
      width="170">
    </el-table-column>
      <el-table-column
      prop="liters"
      label="TOTAL LITERS"
      width="125">
    </el-table-column>
  </el-table>
  <div class="block" style="float:right;padding:10px">
  <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
  </app-pagination>
  </div>
</div>
</template>
<script>
import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import _ from 'lodash'
export default {
  name: 'CustomerCurrentPoints',
  data () {
    return {
      selectedQuery: '',
      timeout: null,
      selectedCustomerGroup: '',
      tableData3: [],
      station: '',
      stations: [],
      currentPage2: 5,
      currentPage3: 5,
      currentPage4: 4,
      search: '',
      selectedCustomer: '',
      loading: false,
      page: 1,
      type: 'points',
      exportValue: 1,
      queryValue: ''
    }
  },
  computed: {
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups',
      pagination: 'pagination'
    }),
    ...mapGetters('customer', {
      customers: 'customers',
      pagination: 'pagination'
    })
  },
  created () {
    this.getCustomerGroup()
    this.getCustomers()
  },
  methods: {
    remoteMethod: _.debounce(function (query) {
      this.loading = true
      this.$store.dispatch('customer/getCustomers', {
        s: query,
        s_field: 'first_name,last_name,barcode',
        page: this.page,
        export: 0,
        type: this.type,
        customer_group: this.selectedCustomerGroup ? this.selectedCustomerGroup : 0
      }).then(() => {
        this.queryValue = query
        this.loading = false
      })
    }, 1000),
    exportExcel () {
      const params = {
        s: this.queryValue ? this.queryValue : '',
        s_field: 'first_name,last_name',
        export: this.exportValue,
        type: this.type,
        customer_group: this.selectedCustomerGroup ? this.selectedCustomerGroup : 0
      }
      if (params !== '') {
        this.url = process.env.VUE_APP_API_ENDPOINT + '/customer?s=' + params.s + '&s_field=' + params.s_field + '&export=' + params.export + '&type=' + params.type + '&customer_group=' + params.customer_group
      }
      window.location.href = this.url
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', {page: null})
    },
    getCustomers () {
      const page = this.page
      this.$store.dispatch('customer/getCustomers', {page})
    },
    handlePagination (page) {
      this.page = page
    },
    selectCustomerEvent () {
      if (this.selectedCustomer === '') {
        this.$store.dispatch('customer/getCustomers', {
          s: this.queryValue ? this.queryValue : '',
          s_field: 'first_name,last_name',
          page: this.page,
          selectedCustomerGroup: this.selectedCustomerGroup ? this.selectedCustomerGroup : 0
        }).then(() => {
          this.loading = false
        })
      }
    },
    selectedCustomerGroupEvent () {
      const page = this.page
      if (this.selectedCustomerGroup !== '') {
        this.$store.dispatch('customer/getCustomers', {
          s: this.queryValue ? this.queryValue : '',
          s_field: 'first_name,last_name',
          customer_group: this.selectedCustomerGroup,
          page,
          export: 0,
          type: this.type
        })
      } else {
        this.$store.dispatch('customer/getCustomers', {
          s: '',
          s_field: 'first_name,last_name',
          customer_group: 0,
          page: this.page,
          export: 0,
          type: this.type
        })
      }
    }
  },
  components: {
    'app-pagination': Pagination
  }
}
</script>
<style scoped>
.demo-input-label {
    display: inline-block;
    width: 60px;
  }
  .el-table th {
    text-align: center;
}
</style>
